<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 class="modal-title">{{ $t('Manage Note') }}</h4>
                    <span class="user-guide ">
                        <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button" aria-expanded="false" style="color: white;" aria-controls="collapseExample">
                            <em></em>User Guide</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t(addUserGuide)"></div>
                            </div>
                        </div>
                    </span>
                    <button type="button" class="close" @click="onCancel">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form ref="dynamicForm" :buttons="buttons" :mandatorytext="$t('MandatoryString')" lang="en" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                    </dynamic-form>

                    <!-- <div class="col-lg-12">
                        <div class="py-1">
                            <span class="mandatory">{{ $t("Mandatory") }}</span>
                        </div>
                    </div> -->
                    <div class="col-lg-12 p-0 float-left mt-4" v-if="NoteId > 0">
                        <div id="Comments" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                            <div id="Expenseaccordion" class="w-100">
                                <div class="card mb-4" v-for="(groupItem, key, index) in groupByDate">
                                    <div class="card-header">
                                        <a :class="{ 'card-link collapsed': index > 1, 'card-link': index <= 1 }" style="color:#000" data-toggle="collapse" :href="'#collapseOneThread' + index">
                                            <div class="conversation-date heading-border"><span>
                                                    {{ headerFormatTime(key) }}</span>
                                            </div>
                                        </a>
                                    </div>
                                    <div :id="'collapseOneThread' + index" :class="{ 'collapse': index > 1, 'collapse show': index <= 1 }">
                                        <div class="card-body" v-for="(item, index) in groupItem">
                                            <div class="conversation-main">

                                                <div class="conversation-time"><span>{{formatTime(item.CREATED_AT)}}</span></div>
                                                <div class="conversation-box-con">
                                                    <div class="conversation-box">
                                                        <div class="name-circle light-yellow">
                                                            <img :src="item.AVATAR ? item.AVATAR : '/Content/images/DefaultImages/avatar-icon.jpg'" alt="Image" width="51" height="51" style="font-size:12px" loading="lazy" />
                                                        </div>
                                                        <div class="conversation-header addednote-ticket">
                                                            <span>
                                                                <span class="sender-name mr-2">{{ item.USER_NAME }} </span>
                                                                <span class="sender-department mr-2">( {{$t('PostedOn')}} :</span><span class="sender-department" style="color : #dc3545!important"> {{
                                                                                        toPrettyString(item.CREATED_AT) }}
                                                                </span> <span class="sender-department">)</span>
                                                            </span>
                                                        </div>
                                                        <div class="conversation-content addednote-ticket">
                                                            <p>{{ item.DESCRIPTION }}</p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                            <dynamic-form ref="validateNoteComment" lang="en" :buttons="commentButtons" :schema="CommentSchema">
                            </dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../services/DataService';
export default ({

    props: {
        UserId: {
            type: Number,
            require: true
        },
        NoteId: {
            type: Number,
            required: false
        },
        RefCode: {
            type: String,
            required: false
        },
        NoteItem: {
            type: Array
        },
        callbackfunction: {
            type: Function
        },
        addUserGuide: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            selectAll: false,
            fileData: [],
            editor: this.CLASSIC_EDITOR,
            department: {
                name: '',
                status: {
                    name: "active",
                    id: 1
                },
                visibaleToclients: false,
                visibaleToTicting: false,
                description: '',
            },

            FormSchema: [{
                    layoutType: 'triple',
                    Data: [{
                            astype: "TextField",
                            label: "Title",
                            name: "Title",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                required: true,
                                max: 100,
                                // "regex": /^[a-zA-Z0-9\s]*$/
                            },
                        },
                        {
                            astype: "RadioField",
                            label: "",
                            name: "Type",
                            value: 1,
                            placeholder: "",
                            validationRules: {
                                "required": false,
                                max: 200
                            },
                            config: {
                                options: [{
                                    name: 'Private',
                                    value: 0
                                }, {
                                    name: 'Public',
                                    value: 1
                                }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: 1001,
                            visibility: true,
                            config: {
                                options: [{
                                    name: 'Active',
                                    value: 1001
                                }, {
                                    name: 'InActive',
                                    value: 1002
                                }],
                                showAddIcon: false,
                            }
                        },
                    ]
                },
                {
                    layoutType: 'single',
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        visibility: true,
                        validationRules: {
                            "required": false,
                            max: 2000,
                            //"regex": /^[a-zA-Z0-9\s]*$/
                        },
                        config: {
                            options: [],
                            showAddIcon: false,
                            rows: 3
                        }
                    }]
                },
            ],
            CommentSchema: [{
                layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Comment'),
                    name: "Comment",
                    value: "",
                    placeholder: this.$t('Enter Comment'),
                    validationRules: {
                        "required": true,
                        max: 2000,
                        // "regex": /^[a-zA-Z0-9\s]*$/
                    },
                    config: {
                        rows: 3
                    }
                }]
            }],
            commentButtons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-paper-plane pr-2'></i>Post New Comment",
                isVisible: true,
                onClick: this.SavePostcomment,
            }],
            isLoading: false,
            noRecordForTab: false,
            NoteId: 0,
            Type: '',
            comment: '',
            CommentData: [],
            CommentExpenseData: [],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel,
                },
            ],
        }
    },
    computed: {
        groupByDate() {
            var vm = this;
            let groups = {};
            vm.CommentData.forEach(item => {
                let date = item.CREATED_AT.substring(0, 10);
                if (groups[date]) {
                    groups[date].push(item);
                } else {
                    groups[date] = [item];
                }
            });

            return groups;
        }
    },

    created: async function () {
        var vm = this;
        if (vm.RefCode == 'Update') {
            if (vm.NoteItem.NOTE_ID > 0) {
                var vm = this;
                await vm.GetCommentList();
                vm.NoteId = vm.NoteItem.NOTE_ID;
                (vm.FormSchema)[0].Data[0].value = vm.NoteItem.TITLE;
                (vm.FormSchema)[0].Data[1].value = vm.NoteItem.NOTEACCESSIBILITY;
                (vm.FormSchema)[0].Data[2].value = vm.NoteItem.STATUS_ID;
                (vm.FormSchema)[1].Data[0].value = vm.NoteItem.DESCRIPTION;
            }
        }
    },
    methods: {
        onChangeRadio: function (e, field) {
            var vm = this;
            vm.Type = e.target.dataset.value;
        },
        closePopup() {
            var vm = this;
            vm.$parent.editNotespopup = false;
        },
        onCancel: function () {
            var vm = this;
            vm.$parent.editNotespopup = false;
        },
        formatDateMonth: function (dateTimeString) {
            let dateTime = new Date(dateTimeString);
            const options = {
                day: '2-digit',
                month: 'long',
                year: 'numeric'
            };
            let formattedDate = dateTime.toLocaleDateString('en-GB', options).replace(' ', '/').replace(' ', '/');
            return formattedDate;
        },
        headerFormatTime: function (dateTimeString) {
            let dateTime = new Date(dateTimeString);

            // Options for formatting day, month, and year
            let options = {
                day: '2-digit',
                month: 'short',
                year: 'numeric'
            };

            // Format date to an array of parts
            let parts = dateTime.toLocaleDateString('en-GB', options).split(' ');

            // Construct the formatted date string with a comma
            let formattedDate = `${parts[0]} ${parts[1]}, ${parts[2]}`;

            return formattedDate;
        },
        formatTime: function (dateTimeString) {
            let dateTime = new Date(dateTimeString);
            let formattedTime = dateTime.toLocaleTimeString('en-US', {
                hour: 'numeric',
                minute: '2-digit',
                hour12: true
            });
            return formattedTime;
        },
        toPrettyString(userDate) {
            const now = new Date();
            const date = new Date(userDate);
            const ts = Math.abs(now - date); // Time difference in milliseconds
            const delta = ts / 1000; // Convert milliseconds to seconds

            if (delta < 60) {
                return 'few seconds ago';
            }
            if (delta < 120) {
                return 'a minute ago';
            }
            if (delta < 2700) { // 45 * 60
                return `${Math.floor(delta / 60)} minutes ago`;
            }
            if (delta < 5400) { // 90 * 60
                return 'an hour ago';
            }
            if (delta < 86400) { // 24 * 60 * 60
                return `${Math.floor(delta / 3600)} hours ago`;
            }
            if (delta < 172800) { // 48 * 60 * 60
                return 'yesterday';
            }
            if (delta < 2592000) { // 30 * 24 * 60 * 60
                return `${Math.floor(delta / 86400)} days ago`;
            }
            if (delta < 31104000) { // 12 * 30 * 24 * 60 * 60
                const months = Math.floor(delta / (30 * 86400));
                return months <= 1 ? '1 month ago' : `${months} months ago`;
            }
            const years = Math.floor(delta / (365 * 86400));
            return years <= 1 ? '1 year ago' : `${years} years ago`;
        },

        async onSubmit(formfields) {
            var vm = this;
            vm.isLoading = true;
            if (vm.NoteItem.NOTE_ID == undefined)
                vm.NoteItem.NOTE_ID = 0;
            let myObj = {
                NoteId: vm.NoteItem.NOTE_ID > 0 ? vm.NoteItem.NOTE_ID : 0,
                Title: formfields.Title,
                Type: vm.Type == '0' ? false : true,
                Status: formfields.Status,
                Description: formfields.Description,
                UserId: vm.UserId
            }
            var Jsondata = JSON.stringify(myObj);
            await DataService.AddUpdateUserNote(Jsondata).then(function (response) {
                if (response != null && response.data != null && response.data > 0) {
                    vm.ShowAlert(vm.$t('RecordsSavedSuccessfully'), 'success');
                    vm.isLoading = false;
                    vm.$parent.FetchData();
                    vm.callbackfunction();
                    vm.closePopup();
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                    vm.isLoading = false;
                    vm.closePopup();
                }
            })

        },
        SavePostcomment() {
            var vm = this;
            vm.$refs.validateNoteComment.validateForm.validate().then(result => {
                if (result) {
                    vm.isLoading = true;
                    var comment = vm.$refs.validateNoteComment.getPropertyValue("Comment", "value");
                    let myObj = {
                        source_name: 'NOTE',
                        comment: comment,
                        source_id: vm.NoteId,
                        status_id: 1001
                    }
                    var Jsondata = JSON.stringify(myObj);
                    DataService.SavePostcomment(Jsondata).then(async function (response) {
                        if (response != null && response.data != null && response.data > 0) {
                            //debugger;
                            await vm.GetCommentList();
                            vm.CommentSchema[0].Data[0].value = '';
                            vm.ShowAlert(vm.$t('CommentSavedSuccessfully'), 'success');
                            vm.isLoading = false;
                            vm.$refs.validateNoteComment.validateForm.reset();
                        } else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }
                    })
                }
            });

        },
        GetCommentList: async function () {
            var vm = this;
            var url = `sourceName=NOTE&sourceId=${vm.NoteId}`;
            await DataService.GetCommentById(url).then((response) => {
                if (response.data != null && response.data != '') {
                    if (response.data.length > 0) {
                        vm.CommentData = response.data;
                    } else {
                        vm.CommentData = [];
                        vm.isLoading = false;
                    }
                } else {
                    vm.CommentData = [];
                    vm.isLoading = false;
                }
            });
        },
    },

});
</script>

<style>
.custom-control-input {
    z-index: 1;
}

textarea#comment.form-control {
    min-height: 100px !important;
}

textarea {
    resize: none !important;
}
</style>
