<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('ManageReview') }}
                            </h5>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>
                        <loader :is-visible="isLoading"></loader>
                        <div class="listing py-3 px-3">
                            <dynamic-form lang="en" ref="refAddReview" :schema="FormSchema"
                                v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                        <div class="col-lg-12" id="divApprovalSequence">
                            <div class="row mb-4">
                                <div class="col-lg-12 mt-3 mb-3">
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span>Reviewer Sequence</span>
                                        </h5>
                                    </div>
                                </div>
                                <div class="listing col-lg-12">
                                    <div class="table-responsive" style="overflow: visible;">
                                        <table class="table table-bordered dt-responsive nowrap mt-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th class="text-left">
                                                        <span>Name</span>
                                                    </th>
                                                    <th class="text-center">
                                                        <span>Rank Up</span>
                                                    </th>
                                                    <th class="text-center">
                                                        <span>Rank Down</span>
                                                    </th>
                                                    <th class="text-left">
                                                        <span>Due Date</span>
                                                    </th>
                                                    <th class="text-center">
                                                        <span>Delete</span>
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody v-if="!showReviewListing">
                                                <tr>
                                                    <td colspan="5"
                                                        class="text-center text-danger no_records no-record">
                                                        No record(s) found
                                                    </td>
                                                </tr>
                                            </tbody>
                                            <tbody v-if="showReviewListing">
                                                <tr v-for="(item,index) in AddReviewerListing" :key="updatereviewer">
                                                    <td class="text-left">
                                                        {{ item.ReviewerName }}
                                                    </td>
                                                    <td class="text-center text-middle single-action">
                                                        <a class="up" @click="ReviewerRankUp(index)" style="font-size: 20px">
                                                            <i class="rankuparrow fa fa-arrow-circle-o-up action-icon"></i>
                                                        </a>
                                                    </td>
                                                    <td class="text-center text-middle single-action">
                                                        <a class="down" @click="ReviewerRankdown(index)" style="font-size: 20px">
                                                            <i class="rankdownarrow fa fa-arrow-circle-o-down action-icon"></i>
                                                        </a>
                                                    </td>
                                                    <td class="text-left"  style="overflow: visible!important;">
                                                        <v-date-picker mode="date" class="input-group" v-model="item.ReviewerDueDate" :masks='{ input:["DD-MM-YYYY"] }'>
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                <input ref="inputDatePicker" class="form-control" :value="inputValue" placeholder="Due Date(MM/DD/YYYY)" v-on:click="togglePopover()"/>
                                                                <div class="input-group-append" v-on:click="togglePopover()"><span class="input-group-text"><i class="fa fa-calendar"></i></span></div>
                                                            </template>
                                                        </v-date-picker>       
                                                    </td>
                                                    <td class="text-center">
                                                        <a class="delete single-action" @click="RemoveReviewerFromList(index)" style="font-size: 20px">
                                                            <i class="fa fa-trash text-danger action-icon"></i>
                                                        </a>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                            <div class="row flex-row-reverse mb-3">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" class="btn btn-success formbtn mr-2" @click="SaveReviewer"><i
                                        class="fa fa-save pr-2"></i><span>Save</span>
                                    </a>
                                    <a class="btn btn-danger text-white formbtn" @click="ClosePopUp"><i
                                        class="fa fa-close text-white pr-2"></i><span>Cancel</span>
                                    </a>
                                </div>
                                <div class="col-lg-6">
                                    <div class="py-2">
                                        <span class="mandatory">Fields marked with an asterisk (*) are
                                            mandatory.</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService'
import moment from 'moment';
export default {
    props: {
        UserId: {
            type: Number
        },
        EditReviewData:{
            type:Array
        }
    },
    data() {
        return {
            isLoading:false,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('CommentTitle'),
                            name: "Title",
                            value: "",
                            placeholder: "Enter Title",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EventStartDate') + ('(M/D/YYYY)'),
                            name: "StartDate",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: "required",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndDateTime') + ('(M/D/YYYY)'),
                            name: "EndDate",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: "required",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('CandidateTillDate') + ('(M/D/YYYY)'),
                            name: "CandidateDueDate",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: {"required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "1001",
                            placeholder: "Enter Status",
                            visibility: true,
                            config: {
                                options: [{ name: 'Active', value: 1001 }, { name: 'InActive', value: 1002 }],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Review_Template'),
                            name: "Review_Template",
                            value: "",
                            placeholder: "Enter Review",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "EnterDescription",
                            value: "",
                            placeholder: "",
                            visibility: true,
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "RadioField",
                            label: this.$t('ReviewProcessing'),
                            name: "ReviewProcessing",
                            value: "1",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [{
                                    value: 1,
                                    name: "Sequential"
                                },
                                {
                                    value: 0,
                                    name: "Non-Sequential"
                                }],
                                showAddIcon: false,
                                onChange: this.GetRadioValue
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Reviewer'),
                            name: "Reviewer",
                            value: "",
                            placeholder: "Enter Reviewer",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.AddReviewerDetail
                            },
                            validationRules: "required",
                        },
                    ]
                }
            ],
            showReviewListing:false,
            AddReviewerListing:[],
            updatereviewer:0,
            ReviewId:null
        }
    },
    async created() {
        var vm = this;
        if(vm.EditReviewData != undefined){
            vm.EditPerformanceReview(vm.EditReviewData);
        }
        await vm.GetReviewTemplate();
        await vm.GetReviewerList();
    },
    methods: {
        ClosePopUp(value) {
            var vm = this;
            vm.$parent.CloseAddReview(value);
        },
        async GetReviewTemplate() {
            var vm = this;
            await DataService.GetReviewTemplateList().then(response => {
                if (response.data.length > 0) {
                    var getvalue = "";
                    response.data.forEach((x, y) => {
                        vm.FormSchema[0].Data[5].config.options.push({
                            name: x.Title,
                            value: x.TemplateId
                        })
                        if (y == 0) {
                            getvalue = x.TemplateId
                        }
                    })
                    vm.$refs.refAddReview.UpdateKeyValue();
                    vm.FormSchema[0].Data[5].value = getvalue;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        async GetReviewerList() {
            var vm = this;
            var params = `userReviewerId=${vm.UserId}`;
            await DataService.GetReviewerList(params).then(response => {
                if (response.data.length > 0) {
                    var getvalue = "";
                    response.data.forEach((x, y) => {
                        vm.FormSchema[2].Data[1].config.options.push({
                            name: x.EmpName,
                            value: x.EmpID
                        })
                        if (y == 0) {
                            getvalue = x.EmpID
                        }
                    })
                    vm.$refs.refAddReview.UpdateKeyValue();
                    vm.FormSchema[2].Data[1].value = getvalue;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        SaveReviewer(){
            var vm = this;

            this.$refs.refAddReview.validateForm.validate().then(async function (valid) {
                if (valid) {

                    setTimeout(() => {                    
                        if(vm.AddReviewerListing.length == 0){
                            vm.ShowAlert(vm.$t('ReviewerNotExist'), 'warning');
                            return;
                        }
                        if(vm.FormSchema[0].Data[3].value=='' || vm.FormSchema[0].Data[3].value==null){
                        
                        }else{
                            if(vm.FormSchema[0].Data[3].value > vm.FormSchema[0].Data[2].value){
                                vm.ShowAlert(vm.FormSchema[0].Data[3].label + ' ' + vm.$t('CandidateDueDateReviewerEndDate'), 'warning');
                                return;
                            }
                            if(vm.FormSchema[0].Data[3].value < vm.FormSchema[0].Data[1].value){
                                vm.ShowAlert(vm.FormSchema[0].Data[3].label + ' ' + vm.$t('ReviewStartDateCandidateDue'), 'warning');
                                return;
                            }
                        }
                    }, 100);

                    var isTrueSequence = true;
                    vm.AddReviewerListing.forEach(x=>{
                        if (x.ReviewerDueDate === '' || x.ReviewerDueDate === null) {
                            isTrueSequence = false;
                            vm.ShowAlert(vm.$t('Please Select Due Date to create a review'), 'warning');
                            vm.buttons[0].class = "btn btn-success";
                            return false;
                        }else{                    
                            if(x.ReviewerDueDate < vm.FormSchema[0].Data[1].value){
                                isTrueSequence = false;
                                vm.ShowAlert(vm.$t('ReviewerSeqDueDate'), 'warning');
                                return;
                            }
                            if(x.ReviewerDueDate > vm.FormSchema[0].Data[2].value){
                                isTrueSequence = false;
                                vm.ShowAlert(vm.$t('ReviewerSeqDueDateEndDate'), 'warning');
                                return;
                            }
                        }
                    })
                    if(isTrueSequence){
                        vm.isLoading = true;
                        vm.AddReviewerListing.forEach(item=>{
                            if(item.ReviewerDueDate != ""){
                            item.ReviewerDueDate = moment(item.ReviewerDueDate).format('YYYY-MM-DD')}
                        })
                        const formatReviewers = (reviewers) => {
                            return reviewers
                            .map(reviewer => `${reviewer.ReviewerId},${reviewer.ReviewerDueDate}`)
                            .join('^');
                        };
                        const result = formatReviewers(vm.AddReviewerListing);

                        var temp = {
                            UserId:vm.UserId,
                            ReviewId:vm.ReviewId,
                            ReviewTemplateId:vm.FormSchema[0].Data[5].value,
                            Title:vm.FormSchema[0].Data[0].value,
                            Description:vm.FormSchema[1].Data[0].value,
                            StartDate:vm.$options.filters.formatDate(vm.FormSchema[0].Data[1].value),
                            EndDate:vm.$options.filters.formatDate(vm.FormSchema[0].Data[2].value),
                            Approvers:result,
                            StatusId:vm.FormSchema[0].Data[4].value,
                            IsSequentialProcessing:(vm.FormSchema[2].Data[0].value=="1")? true : false,
                            CandidateTillDate:vm.$options.filters.formatDate(vm.FormSchema[0].Data[3].value)
                        }
                        DataService.SaveReview(temp).then(async response=>{
                            if(response.data == true || response != ""){
                                if (typeof (response.data.Message) != 'undefined') {
                                    if (response.data.Message = 'Exit') {
                                        vm.ShowAlert(response.data.Data, "failure", vm.$t('Alert'));
                                        // vm.ShowAlert(vm.$t(response.data.Result[0].TITLE + " Review Already"), "warning", vm.$t('Alert'));
                                    } else if (response.data.Message = 'Overlap') {
                                        vm.ShowAlert(response.data.Data, "failure", vm.$t('Alert'));
                                    }                                    
                                }
                                else if(vm.EditReviewData){
                                    vm.ShowAlert(vm.$t("Review has been successfully ") + ' Updated', "success", vm.$t('Alert'));
                                    // vm.ShowAlert(vm.$t('CAND_F_COMMENT_R'), 'success');
                                        vm.ClosePopUp('Save');
                                }else{
                                    vm.ShowAlert(vm.$t("Review has been successfully ") + ' Added', "success", vm.$t('Alert'));
                                    // vm.ShowAlert(vm.$t('CAND_F_COMMENT_R'), 'success');
                                        vm.ClosePopUp('Save');
                                }                                
                            }else{
                                vm.ShowAlert(vm.$t('ErrorSavingPleaseTryAgain'), 'failure');
                            }
                            vm.isLoading = false;
                        }).catch(err=>{
                            console.log(err);
                        })
                    }else{return;}
                }
            })
        },
        AddReviewerDetail(x,y){
            var vm = this;
            var checkReviewerExist = vm.AddReviewerListing.find(x=> x.ReviewerId == y.value);
            if(checkReviewerExist != undefined){
                vm.ShowAlert(vm.$t('SelectReviewer'), 'failure');
                return;
            }

            var checkexist = vm.FormSchema[2].Data[1].config.options.find(x=> x.value == y.value);
            if(checkexist != undefined){
                vm.AddReviewerListing.push({ReviewerId:checkexist.value,ReviewerName:checkexist.name,ReviewerDueDate:""})
                vm.updatereviewer += 1;
            }

            if(vm.AddReviewerListing.length > 0){
                vm.showReviewListing = true;
            }
        },
        RemoveReviewerFromList(index){
            var vm = this;
            vm.AddReviewerListing.splice(index,1);
            if(vm.AddReviewerListing.length == 0){
                vm.showReviewListing = false;
            }
        },
        ReviewerRankUp(index){
            var vm = this;
            if(index != 0){
                if(vm.AddReviewerListing[index].ReviewerDueDate == "" && vm.AddReviewerListing[index - 1].ReviewerDueDate == ""){

                }
                else if(vm.AddReviewerListing[index].ReviewerDueDate == ""){
                    vm.ShowAlert(vm.$t('CurrentReviewerTillDateIsRequired'), 'warning');
                    return;
                }
                var temp = vm.AddReviewerListing[index - 1];
                vm.AddReviewerListing[index - 1] = vm.AddReviewerListing[index];
                vm.AddReviewerListing[index] = temp;
                vm.updatereviewer += 1;
            }
        },
        ReviewerRankdown(index){
            var vm = this;
            if(vm.AddReviewerListing.length > 1){
                if(vm.AddReviewerListing[index].ReviewerDueDate == "" && vm.AddReviewerListing[index + 1].ReviewerDueDate == ""){

                }
                else if(vm.AddReviewerListing[index + 1].ReviewerDueDate == ""){
                    vm.ShowAlert(vm.$t('NextReviewerTillDateIsRequired'), 'warning');
                    return;
                }
                var temp = vm.AddReviewerListing[index + 1];
                vm.AddReviewerListing[index + 1] = vm.AddReviewerListing[index];
                vm.AddReviewerListing[index] = temp;
                vm.updatereviewer += 1;
            }
        },
        EditPerformanceReview(data){
            var vm = this;
            vm.FormSchema[0].Data[5].visibility = false;
            vm.ReviewId = data.ReviewerData[0].review_id;
            // vm.$refs.refAddReview.updateProperty("Title", "value", data.ReviewerData[0].title);
            // vm.$refs.refAddReview.updateProperty("StartDate", "value", data.ReviewerData[0].start_date);
            // vm.$refs.refAddReview.updateProperty("EndDate", "value", data.ReviewerData[0].end_date);
            // vm.$refs.refAddReview.updateProperty("CandidateDueDate", "value", data.ReviewerData[0].CANDIDATE_TILL_DATE);
            // vm.$refs.refAddReview.updateProperty("Status", "value", data.ReviewerData[0].STATUS_ID);
            // vm.$refs.refAddReview.updateProperty("EnterDescription", "value", data.ReviewerData[0].description);
            // vm.$refs.refAddReview.updateProperty("ReviewProcessing", "value", data.ReviewerData[0].IS_SEQUENTIAL_PROCESSING);
            // vm.$refs.refAddReview.UpdateKeyValue();
            vm.FormSchema[0].Data[0].value = data.ReviewerData[0].title;
            vm.FormSchema[0].Data[1].value = data.ReviewerData[0].start_date;
            vm.FormSchema[0].Data[2].value = data.ReviewerData[0].end_date;
            vm.FormSchema[0].Data[3].value = data.ReviewerData[0].CANDIDATE_TILL_DATE;
            vm.FormSchema[0].Data[4].value = data.ReviewerData[0].STATUS_ID;
            vm.FormSchema[1].Data[0].value = data.ReviewerData[0].description;
            vm.FormSchema[2].Data[0].value = data.ReviewerData[0].IS_SEQUENTIAL_PROCESSING;
            data.ReviewerSequenceData.forEach(x=>{
                x.TILL_DATE = x.TILL_DATE != null ? moment(x.TILL_DATE).format('YYYY-MM-DD') : "";
                vm.AddReviewerListing.push({ReviewerId:x.REVIEWER_ID,ReviewerName:x.REVIEWER_NAME,ReviewerDueDate:x.TILL_DATE});
                vm.updatereviewer += 1;
            })
            if(vm.AddReviewerListing.length > 0){
                vm.showReviewListing = true;
            }
        }
    }
}
</script>