<template lang="en">
<section>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Add Goal') }}</h4>
                        <button type="button" class="close" @click="Cancel">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="col-lg-12 p-0">

                            <dynamic-form ref="validateGoal" lang="en" :buttons="false" :schema="FormSchema" @OnSubmit="onSubmit">
                            </dynamic-form>
                            <div class="form-group" v-if="GoalType == 'Update'">
                                <label for="transactionSize">{{ $t('Progress') }}: <span style="border: 0; color: #f6931f; font-weight: bold;">{{ progress}}%</span></label>
                                <div class="slider-container pt-3 ml-2 disabled">
                                    <VueSlider min="0" id="progress" @input="onProgressSlide" max="100" v-model="progress" />
                                </div>
                            </div>
                            <div class="form-group">
                                <label for="transactionSize">{{ $t('ProficiencyByManager') }}: <span style="border: 0; color: #f6931f; font-weight: bold;">{{ proficiencyManager
                                            }}%</span></label>
                                <div class="slider-container pt-3 ml-2">
                                    <VueSlider min="0" id="proficiencyManager" @input="onSlide" max="100" v-model="proficiencyManager" />
                                </div>
                            </div>

                        </div>

                        <div class="row flex-row-reverse mt-3">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="SaveData()" title="Save"><em class="fa fa-save pr-2"></em>{{ $t('Save') }}</a>
                                <a href="javascript:;" class="btn btn-danger mr-2" v-on:click="Cancel()" title="Cancel"><em class="fa fa-close pr-2"></em>{{ $t('Cancel') }}</a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">                                    
                                        <span class="mandatory">{{ $t("Mandatory") }}</span>                                    
                                </div>
                            </div>
                        </div>

                        <div class="conversation-date heading-border mt-4"><span>{{ $t("Proficiency History by Managers")}}</span></div>

                        <div class="container-fluid collapse show p-0" id="collapseOne1">
                            <div class="listing">
                                <div class="table-responsive">
                                    <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th><span>{{ $t('Name') }}</span></th>
                                                <th class="text-center"><span>{{ $t('Date') }} </span></th>
                                                <th class="text-center"><span>{{ $t('Proficiency_only') }}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="ProficiencyHistoryList.length === 0">
                                                <td class="text-center text-danger no-record" colspan="8">
                                                    {{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                            <tr v-else v-for="(item, index) in ProficiencyHistoryList" :key="index">
                                                <td>{{ item.MANAGER_NAME }}</td>
                                                <td class="text-center">
                                                    {{ $options.filters.formatDate(item.PROGRESS_BY_MANAGER_ON) }}
                                                </td>
                                                <td class="text-center">{{ item.PROGRESS_BY_MANAGER }}<span>%</span>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>

                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 mt-3 float-left" id="DivComment" v-if="GoalType == 'Update'">
                            <div id="accordion" class="w-100">
                                <div class="card mb-4" v-for="(groupItem, key, index) in groupByDate">
                                    <div class="card-header">
                                        <a :class="{ 'card-link collapsed': index > 1, 'card-link': index <= 1 }" style="color:#000" data-toggle="collapse" :href="'#collapseOneThread' + index">
                                            <div class="conversation-date heading-border"><span>{{ headerFormatTime(key) }} </span></div>
                                        </a>
                                    </div>
                                    <div>
                                        <div :id="'collapseOneThread' + index" :class="{ 'collapse': index > 1, 'collapse show': index <= 1 }">
                                            <div class="card-body">
                                                <div class="conversation-main">

                                                    <template v-for="(item, index) in groupItem">
                                                        <div class="conversation-time">
                                                            <span>{{ formatTime(item.CREATED_AT) }}</span>
                                                        </div>
                                                        <div class="conversation-box-con">
                                                            <div class="conversation-box">
                                                                <div class="name-circle light-yellow">
                                                                    <img :src="item.AVATAR ? item.AVATAR : '/Content/images/DefaultImages/avatar-icon.jpg'" alt="Image" width="51" height="51" style="font-size:12px" loading="lazy" />
                                                                </div>
                                                                <div class="conversation-header addednote-ticket">
                                                                    <span class="sender-info">
                                                                        <span class="sender-name mr-2">{{ item.USER_NAME }}</span>
                                                                        <span class="sender-department mr-2">( {{$t('PostedOn')}} :</span><span class="sender-department" style="color : #dc3545!important"> {{
                                                                                        toPrettyString(item.CREATED_AT) }}
                                                                        </span> <span class="sender-department">)</span>
                                                                    </span>
                                                                </div>
                                                                <div class="conversation-content addednote-ticket">
                                                                    <p>{{ item.DESCRIPTION }}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </template>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="mt-3 col-sm-12 col-md-12 float-left col-lg-12 col-xl-12 p-0">
                                <dynamic-form ref="validateManageComment" lang="en" :buttons="commentButtons" :schema="CommentSchema">
                                </dynamic-form>
                            </div>
                        </div>

                    </div>

                </div>
            </div>
        </div>
    </div>

</section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        UserId: {
            type: Number,
            required: false
        },
        GoalId: {
            type: Number,
            required: false
        },
        callbackfunction: {
            type: Function
        },
        GoalType: {
            type: String,
            required: false
        },
        GoalArray: {
            type: Array,
            required: false
        }
    },
    data() {
        return {
            proficiencyManager: 0,
            noRecord: false,
            TotalRecords: 0,
            isLoading: false,
            CommentList: [],
            progress: 0,
            startDate: '',
            endDate: '',
            ProficiencyHistoryList: [],
            FormSchema: [{
                    layoutType: "four",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true,
                                //"regex": /^[a-zA-Z0-9\s]*$/,
                                max: 100,
                            },
                            disabled: false,
                        },
                        {

                            astype: "DateField",
                            label: this.$t("Start Date"),
                            name: "StartDate",
                            value: new Date(),
                            placeholder: this.$t("StartDate"),
                            validationRules: {
                                required: true,
                            },
                            config: {
                                mode: "date",
                                format: "M/D/YYYY"
                            },

                        },

                        {

                            astype: "DateField",
                            label: this.$t("End Date"),
                            name: "EndDate",
                            value: new Date(),
                            placeholder: this.$t("EndDate"),
                            validationRules: {
                                required: true,
                            },
                            config: {
                                mode: "date",
                                format: "M/D/YYYY"
                            },

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "active",
                            placeholder: "",
                            config: {
                                options: [{
                                        value: "active",
                                        name: 'Active'
                                    },
                                    {
                                        value: "inactive",
                                        name: 'Inactive'
                                    }
                                ]

                            },
                            validationRules: {
                                "required": true
                            },
                            disabled: false,
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [{
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            config: {
                                rows: 3
                            },
                            placeholder: this.$t(''),
                            validationRules: {
                                "required": true,
                                max: 2000,
                               // "regex": /^[a-zA-Z0-9\s]*$/
                            },
                        }

                    ]
                },
            ],
            CommentSchema: [{
                layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Comment'),
                    name: "Comment",
                    value: "",
                    config: {
                        rows: 3
                    },
                    placeholder: this.$t('Enter Comment'),
                    validationRules: {
                        "required": true,
                       // "regex": /^[a-zA-Z0-9\s]*$/
                    },
                }]
            }],
            commentButtons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<em class='fa fa-paper-plane pr-2'></em>Post New Comment",
                isVisible: true,
                onClick: this.SaveComment,
            }],

        }
    },
    computed: {

        groupByDate() {
            var vm = this;
            let groups = {};
            vm.CommentList.forEach(item => {
                let date = item.CREATED_AT.substring(0, 10);
                if (groups[date]) {
                    groups[date].push(item);
                } else {
                    groups[date] = [item];
                }
            });

            return groups;
        }
    },
    async created() {
        var vm = this;
        if (vm.GoalType == 'Update') {
            await vm.GetProficiencyHistoryList();
            await vm.GetCommentList();
            if (vm.GoalArray.row != null) {
                vm.proficiencyManager = vm.GoalArray.row.PROGRESS_BY_MANAGER;
                vm.progress = vm.GoalArray.row.PROGRESS;
                vm.$refs.validateGoal.updateProperty("Title", "value", vm.GoalArray.row.TITLE);
                vm.$refs.validateGoal.updateProperty("StartDate", "value", vm.GoalArray.row.STARTDATE);
                vm.$refs.validateGoal.updateProperty("EndDate", "value", vm.GoalArray.row.ENDDATE);
                vm.$refs.validateGoal.updateProperty("Status", "value", vm.GoalArray.row.STATUS_ID == 1001 ? 'active' : 'inactive');
                vm.$refs.validateGoal.updateProperty("Description", "value", vm.GoalArray.row.DESCRIPTION);
                vm.$refs.validateGoal.UpdateKeyValue();
            }
        }
    },
    methods: {
        SaveData() {
            var vm = this;
            var objData = {
                goalId: vm.GoalId,
                userId: vm.UserId,
                title: vm.$refs.validateGoal.getPropertyValue("Title", "value"),
                startDate: vm.$refs.validateGoal.getPropertyValue("StartDate", "value"),
                endDate: vm.$refs.validateGoal.getPropertyValue("EndDate", "value"),
                description: vm.$refs.validateGoal.getPropertyValue("Description", "value"),
                statusId: vm.$refs.validateGoal.getPropertyValue("Status", "value"),
                progress: vm.progress,
                progressByManager: vm.proficiencyManager
            }
            var obj = JSON.stringify(objData);
            var params = {
                objData: obj
            };
            // var params = `objData=${obj}`;
            vm.$refs.validateGoal.validateForm.validate().then(result => {
                if (result) {
                    DataService.AddUpdateGoal(params).then(response => {
                        if (response.data != null) {
                            if (response.data.Status == "Success" && response.data.Code == "1") {
                                vm.ShowAlert(vm.$t('AddedUserGoal'), "success");
                                vm.Cancel();
                                vm.callbackfunction();
                                vm.isLoading = false;
                            } else if (response.data.Status == "Success" && response.data.Code == "2") {
                                vm.ShowAlert(vm.$t('UpdatedUserGoal'), "success");
                                vm.Cancel();
                                vm.callbackfunction();
                                vm.isLoading = false;
                            } else {
                                vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                vm.isLoading = false;
                            }
                        }
                    });
                }
            });
        },
        headerFormatTime: function (dateTimeString) {
            let dateTime = new Date(dateTimeString);

            // Options for formatting day, month, and year
            let options = {
                day: '2-digit',
                month: 'short',
                year: 'numeric'
            };

            // Format date to an array of parts
            let parts = dateTime.toLocaleDateString('en-GB', options).split(' ');

            // Construct the formatted date string with a comma
            let formattedDate = `${parts[0]} ${parts[1]}, ${parts[2]}`;

            return formattedDate;
        },
        toPrettyString(userDate) {
            //debugger;
            const now = new Date();
            const date = new Date(userDate);
            const ts = Math.abs(now - date); // Time difference in milliseconds
            const delta = ts / 1000; // Convert milliseconds to seconds

            if (delta < 60) {
                return 'few seconds ago';
            }
            if (delta < 120) {
                return 'a minute ago';
            }
            if (delta < 2700) { // 45 * 60
                return `${Math.floor(delta / 60)} minutes ago`;
            }
            if (delta < 5400) { // 90 * 60
                return 'an hour ago';
            }
            if (delta < 86400) { // 24 * 60 * 60
                return `${Math.floor(delta / 3600)} hours ago`;
            }
            if (delta < 172800) { // 48 * 60 * 60
                return 'yesterday';
            }
            if (delta < 2592000) { // 30 * 24 * 60 * 60
                return `${Math.floor(delta / 86400)} days ago`;
            }
            if (delta < 31104000) { // 12 * 30 * 24 * 60 * 60
                const months = Math.floor(delta / (30 * 86400));
                return months <= 1 ? '1 month ago' : `${months} months ago`;
            }
            const years = Math.floor(delta / (365 * 86400));
            return years <= 1 ? '1 year ago' : `${years} years ago`;
        },
        formatTime(dateTimeString) {
            // Create a Date object from the provided date and time string
            let dateTime = new Date(dateTimeString);

            // Format the time using toLocaleTimeString()
            let formattedTime = dateTime.toLocaleTimeString([], {
                hour: '2-digit',
                minute: '2-digit'
            });

            return formattedTime;
        },
        SaveComment() {
            var vm = this;
            var comment = vm.$refs.validateManageComment.getPropertyValue("Comment", "value");
            var params = `userSkillId=${vm.GoalId}&sourceName=GOAL&comment=${comment}`
            vm.$refs.validateManageComment.validateForm.validate().then(result => {
                if (result) {
                    DataService.SaveComment(params).then((response) => {
                        if (response.data != null) {
                            if (response.data.Msg == "INSERT" && response.data.Code == "1") {
                                vm.ShowAlert(vm.$t('UserProgress'), "success");
                                vm.GetCommentList();
                                vm.CommentSchema[0].Data[0].value = '';
                                vm.isLoading = false;
                                vm.$refs.validateManageComment.validateForm.reset();
                            } else {
                                vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                vm.isLoading = false;
                            }
                        }
                    });
                }
            });

        },
        async GetCommentList() {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `userSkillId=${vueObj.GoalId}&sourceName=GOAL`
            await DataService.CommentList(params).then(response => {
                if (response.data.Status = "Success") {
                    if (response.data != "" && response.data != null) {
                        if (response.data.Result.data.length > 0) {
                            vueObj.CommentList = response.data.Result.data;
                            vueObj.TotalRecords = response.data.Result.data[0].TOTALRECORDS;
                            vueObj.isLoading = false;
                        } else {
                            vueObj.TotalRecords = 0
                            vueObj.CommentList = [];
                            vueObj.isLoading = false;
                        }
                    } else {
                        vueObj.TotalRecords = 0
                        vueObj.CommentList = [];
                        vueObj.isLoading = false;
                    }
                }
            })
        },
        async GetProficiencyHistoryList() {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `goalId=${vueObj.GoalId}`;
            await DataService.GetProficiencyHistoryList(params).then(response => {
                if (response.data.Status = "Success") {
                    if (response.data != "" && response.data != null) {
                        if (response.data.Result.data.length > 0) {
                            vueObj.ProficiencyHistoryList = response.data.Result.data;
                            vueObj.TotalRecords = response.data.Result.data[0].TOTAL_RECORDS;
                            vueObj.noRecord = false;
                            vueObj.isLoading = false;

                        } else {
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                            vueObj.ProficiencyHistoryList = [];
                            vueObj.isLoading = false;
                        }
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.ProficiencyHistoryList = [];
                        vueObj.isLoading = false;
                    }
                }
            })
        },
        Cancel() {
            var vm = this;
            vm.$parent.isAddUserGoal = false;
        },
        onSlide() {
            var vm = this;
            vm.proficiencyManager = this.proficiencyManager;
        },
        onProgressSlide() {
            var vm = this;
            vm.progress = this.progress;
        }
    }
}
</script>

<style>
textarea {
    resize: none !important;
}
</style>
