<template>
<div>
    <div class="listing pt-2">
        <div class="expand-list">
            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                <i class="icon-solid arrow-solid-left text-white"></i>
            </a>
        </div>
        <loader :is-visible="isLoading"></loader>
        <div class="table-responsive table-fix-header" id="div_forListViews">
            <table class="table table-bordered mt-0" id="tblFileList">
                <thead class="thead-bg">
                    <tr>
                        <th class="text-center">
                            <div class="custom-control custom-checkbox">
                                <input class="chkItems custom-control-input" v-on:click="checkboxChecked(null,'all')" v-model="searchcheck_all" type="checkbox" id="chkedAll_0" name="checkbox">
                                <label class="custom-control-label universal-custom-control-label pt-1" for="chkedAll_0"></label>
                            </div>
                        </th>
                        <!-- <th style="width:50px !important;" class="text-center">
                                <input id="chkAllManage" class="chkitems" value="" type="checkbox" v-on:change="onChange('chkAllCheckbox')"> 
                                <label class="form-check form-check-inline">
                                    <div class="custom-control custom-checkbox custom-control-inline">
                                        <input id="chkAllManage" value="" type="checkbox" class="chkItems custom-control-input" v-on:change="onChange('chkAllCheckbox')">
                                        <label for="chkAllManage" class="custom-control-label"></label>
                                </div></label>
                            </th> -->
                        <th class="text-left">
                            <span>Title</span>
                        </th>
                        <th class="text-left">
                            <span>Manager</span>
                        </th>
                        <th class="text-left">
                            <span>Start Date</span>
                        </th>
                        <th class="text-left">
                            <span>End Date</span>
                        </th>
                        <th class="text-center">
                            <span>Progress</span>
                        </th>
                        <th class="text-center">
                            <span>Status</span>
                        </th>
                    </tr>
                </thead>
                <tbody v-if="getPerformancelist.length == 0">
                    <tr>
                        <td colspan="5" class="text-center text-danger no_records no-record">
                            No record(s) found
                        </td>
                    </tr>
                </tbody>
                <tbody v-else>
                    <tr v-for="(item, index) in getPerformancelist" :key="index">
                        <td class="text-center">
                            <div class="custom-control custom-checkbox">
                                <input class="chkiteminner custom-control-input" :value="item.REVIEW_ID" v-model="item.ischecked" v-on:click="checkboxChecked(item, 'single')" :id="'chkAll_'+index" type="checkbox" />
                                <label class="custom-control-label universal-custom-control-label" :for="'chkAll_'+index"></label>
                            </div>
                        </td>
                        <!-- <td style="width:50px !important;" class="text-center">
                                 <input id="chkAll" class="chkiteminner" :value="item.REVIEW_ID" type="checkbox" v-on:change="onChange('innerChkBoxes')"> 
                                <label class="form-check form-check-inline">
                                    <div class="custom-control custom-checkbox custom-control-inline">
                                        <input :id="'chkAll'+index" type="checkbox" class="chkiteminner custom-control-input" v-model="item.ischecked"  v-on:change="onChange('innerChkBoxes')">
                                        <label :for="'chkAll'+index" class="custom-control-label"></label>
                                    </div>
                                </label>    
                            </td> -->
                        <td class="text-left">
                            <a class="text-center" @click="EditReview(item)">
                                {{ item.TITLE }}
                            </a>
                        </td>
                        <td class="text-left">
                            <span class="text-center">
                                {{ item.MANAGER }}
                            </span>
                        </td>
                        <td class="text-left">
                            <span class="text-center">
                                {{ $options.filters.formatDate(item.START_DATE) }}
                            </span>
                        </td>
                        <td class="text-left">
                            <span class="text-left">
                                {{ $options.filters.formatDate(item.END_DATE) }}
                            </span>
                        </td>
                        <td class="text-center">
                            <span class="text-center">
                                {{ item.PROGRESS != null ? item.PROGRESS + '%' : 0 + '%' }}
                            </span>
                        </td>
                        <td class="text-center">
                            <span class="text-center text-overflow-dynamic-container">
                                <div class="cstm-drop-btn" :item="item.REVIEW_ID">
                                    <select id="crmlistingStatus_span" :value="item.STATUS_ID" @change="changestatus(item, $event)" class="w-100" :style="{
                                                'background-color': item.STATUS_ID == '1001' ? 'rgb(40, 167, 69)' : 'rgb(215, 36, 53)',
                                                'color': 'rgb(255, 255, 255)'
                                            }">
                                        <option value="1001"> Active </option>
                                        <option value="1002"> Inactive </option>
                                    </select>
                                </div>
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <modalAddPerformanceReview v-if="showAddPerformanceReview" :UserId="UserId" :EditReviewData="EditReviewData">
    </modalAddPerformanceReview>
</div>
</template>

<script>
import DataService from '../../services/DataService';
import modalAddPerformanceReview from '../../components/Modals/modalAddPerformanceReview.vue';
export default {
    components: {
        modalAddPerformanceReview
    },
    props: {
        UserId: {
            type: Number
        }
    },
    data() {
        return {
            PageSize: 10,
            PageNumber: 1,
            sortBy: null,
            sortExp: null,
            getPerformancelist: [],
            showAddPerformanceReview: false,
            EditReviewData: [],
            isLoading: false,
            searchcheck_all: false,
        }
    },
    async created() {
        var vm = this;
        await vm.GetReviewListing();
    },
    methods: {
        async GetReviewListing() {
            var vm = this;
            vm.isLoading = true;
            var params = `userReviewId=${vm.UserId}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}`;
            await DataService.GetPerformanceReviewList(params).then(response => {
                vm.getPerformancelist = response.data;
                vm.isLoading = false;
            }).catch(err => {
                console.log(err);
            })
        },
        // onChange: function (value) {
        //     if (value == 'chkAllCheckbox') {
        //         var array = document.getElementsByClassName('chkItems custom-control-input');
        //         for (var i = 0; i < array.length; i++) {
        //             if (array[i].type == "checkbox") {
        //                 if (array[i].checked) {
        //                     var innerarray = document.getElementsByClassName('chkiteminner custom-control-input');
        //                     for (var i = 0; i < innerarray.length; i++) {
        //                         if (innerarray[i].type == "checkbox") {
        //                             innerarray[i].checked = true;
        //                         }
        //                     }
        //                 } else {
        //                     var innerarray = document.getElementsByClassName('chkiteminner custom-control-input');
        //                     for (var i = 0; i < innerarray.length; i++) {
        //                         if (innerarray[i].type == "checkbox") {
        //                             innerarray[i].checked = false;
        //                         }
        //                     }
        //                 }
        //             }
        //         }
        //     } else if (value == 'innerChkBoxes') {
        //         var array = document.getElementsByClassName('chkiteminner custom-control-input');
        //         var innerarray = document.getElementsByClassName('chkItems custom-control-input');
        //         for (var i = 0; i < array.length; i++) {
        //             if (array[i].type == "checkbox") {
        //                 if (array[i].checked) {
        //                     innerarray[0].checked = true;
        //                 } else {
        //                     innerarray[0].checked = false;
        //                     break;
        //                 }
        //             }
        //         }
        //     }
        //     var checkCount = $('.chkiteminner.custom-control-input:checked:checked').not("[id^='chkAllManage']").length;
        //     if (checkCount > 0) {
        //         this.$emit('IsDeleteMultiple', true);
        //     }
        //     else {
        //         this.$emit('IsDeleteMultiple', false);
        //     }
        // },
        GetReviewData() {
            var vm = this;
            var val = "";
            $('.chkiteminner:checkbox:checked').not("[id^='chkAllManage']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            })
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Folder")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "",
                        subModuleCode: "",
                        refCode: "Performance_AddReview"
                    };
                    vm.isLoading = true;
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonDelete(objectPostString).then(async function (response) {
                        if (response.status == 200) {
                            $('.chkiteminner:checkbox:checked,#chkAllManage').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple_New").addClass("disabled");
                            await vm.GetReviewListing();
                        }
                        vm.isLoading = false;
                    });
                });
            }
        },
        async EditReview(data) {
            var vm = this;
            var params = `userReviewerId=${data.REVIEW_ID}`;
            await DataService.GetEditReviewerList(params).then(response => {
                if (response.data != "" && response.data != null) {
                    vm.EditReviewData = response.data;
                    vm.showAddPerformanceReview = true;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        CloseAddReview() {
            var vm = this;
            vm.showAddPerformanceReview = false;
        },
        changestatus(data, e) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmForChangeStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                var params = `reviewId=${data.REVIEW_ID}&statusId=${parseInt(e.target.value)}`;
                DataService.UpdateReviewStatus(params).then(async response => {
                    if (response.status == 200 && response.data == true) {
                        await vm.GetReviewListing();
                    }
                }).catch(err => {
                    console.log(err);
                })
            })
        },

        checkboxChecked(itemOrType, type) {          
            if (type === 'all') {
                if ($('#chkedAll_0').prop('checked')) {
                    this.getPerformancelist.forEach(item => {
                        item.ischecked = true;
                        this.searchcheck_all = true;
                        this.$emit('IsDeleteMultiple', true);
                    });
                } else {
                    this.getPerformancelist.forEach(item => {
                        item.ischecked = false;
                        this.searchcheck_all = false;
                        this.$emit('IsDeleteMultiple', false);
                    });
                }
            } else if (type === 'single') {
                // Toggle individual checkbox
                itemOrType.ischecked = !itemOrType.ischecked;
                this.$emit('IsDeleteMultiple', itemOrType.ischecked);
            }
            this.updateSelectAllState(type);
        },

        updateSelectAllState(type) {
            // Check if all feedback items are selected
            if (type === 'single') {
                const allChecked = this.getPerformancelist.every(item => item.ischecked);
                this.searchcheck_all = allChecked;
            } else {
                if (this.searchcheck_all) {
                    var innerarray = document.getElementsByClassName('chkiteminner custom-control-input');
                    for (var i = 0; i < innerarray.length; i++) {
                        if (innerarray[i].type == "checkbox") {
                            innerarray[i].checked = true;
                             this.$emit('IsDeleteMultiple', true);
                        }
                    }

                }
            }

        },
    }
}
</script>
