<template>
    <section>
        <tg-list ModuleName="User" SubModuleCode="Goal"
                                    IdentityColumn="GOALID" :listType="['List']" :ListData="UserGoalListing"
                                    :HeaderData="Headers" 
                                    :TotalRecords="TotalRecords"
                                    :IsShowAction="true" 
                                    :RenderRowActionMethod="renderActions"
                                    v-on:ActionButtonClick="actionButtonClick"
                                    :showCheckBox="true"
                                    :ListDataCallBackFunction="FetchRecords"
                                    :NorecordfoundText="$t('NoRecordfound')"
                                     v-on:PagerButtonClick="pagerMethod" :HeaderButtons="HeaderButtons"
                                      v-on:SortdataButtonClick="sortdata" v-on:HeaderButtonClick="commonHeaderButtonClick">
                                    <template v-slot:slotdata="{ data }">
                                        <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                                            <listing-status :isfor="'GOAL'" v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')"
                                                ref="updatestatus" v-bind:dataid="data.row.GOALID" data-controller="Crm"
                                                v-bind:statuslist="StatusListForListing" v-bind:item="data.STATUS_ID" data-action="index"
                                                v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                                                v-bind:callbackfunction="FetchRecords" v-bind:isselectedvaluestring="true">
                                            </listing-status>
                                            <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')">
                                                <span v-bind:class="getStatusClass(data.row.STATUS_ID, 'STATUS_ID')">
                                                    {{ data.row[data.column.COLUMN_NAME] }}
                                                </span>
                                            </a>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'PROGRESS'">
                                            <span>
                                                {{data.row.PROGRESS }}<span>%</span>
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'STARTDATE'">
                                            <span>
                                                {{$options.filters.formatDate(data.row.STARTDATE) }}
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'ENDDATE'">
                                            <span>
                                                {{$options.filters.formatDate(data.row.ENDDATE) }}
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'TITLE'">
                                            <a href="javascript:;" v-on:click="ManageGoal(data)" ><i
                                                aria-hidden="true" ></i>{{
                                                    data.row.TITLE }}
                                            </a>
                                        </template>
                                    </template>
        </tg-list>
        <AddGoal v-if="isAddUserGoal" :GoalType="GoalType" :GoalArray="GoalArray" :GoalId="GoalId" :UserId="UserId" :callbackfunction="FetchRecords"></AddGoal>
    </section>
</template>
<script>

import DataService from '../../services/DataService';
import AddGoal from "../../components/Modals/AddGoal.vue";
export default {
    components: {   
        AddGoal
    },
    props:{
        UserId: {
            type: Number,
            required: false
        }
    },
    data() {
        return {
            noRecords: false,
            GoalId :0,
            GoalArray:[],
            isAddUserGoal:false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            Headers: [{
                "COLUMN_NAME": "TITLE",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Title",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "STARTDATE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Start Date",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "ENDDATE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "End Date",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "PROGRESS",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "PROGRESS",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "ASSIGNED_BY",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created by",
                "DISPLAY_ORDER": 5
            },
            {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 6
            }

            ],
            HeaderButtons :
            [
                {
                    title: 'Add Goal',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'openAddGoal',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true,
                    id: 'AddGoal',
                },
                {
                    title: 'Remove Goal',
                    iconClass: 'fa-trash-o',
                    callbackfunction: 'commonDeleteData',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: false,
                    id: 'DeleteMultiple',
                    isEnabledOnSelectedRow: true
                }
            ],
            SortBy: '',
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            SortExp: '',
            PageSize: "10",
            GoalType:'',
            listActions:[   
                {
                    title: 'Delete',
                    href: 'javascript:void(0)',
                    icon: 'fa fa-trash',
                    callbackfunction: 'DeleteGoal',
                    additionalSpan: ''
                }
            ],
            PageNumber: "1",
            GroupStart: 1,
            GroupEnd: 3,
            UserGoalListing: []
           
        }
    },
    updated(){
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t("Goal");
            divElement[0].appendChild(spanElement);
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchRecords();
    },
    methods: {
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteUserGoal();
                    break;
                case "openAddGoal":
                    this.openAddGoal();
                    break;
            }
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event,isChild) {            
            switch (callbackfunction) {
                case 'DeleteGoal':
                     this.DeleteGoal(item.GOALID);
                    break;
            }
        },
        ManageGoal: function(data){
            var vm=this;
            vm.GoalId = data.row.GOALID;
            vm.GoalType ='Update';
            vm.GoalArray =data;
            vm.isAddUserGoal= true;
        },
        openAddGoal:function(){
            var vm=this;
            vm.GoalType ='Insert';
            vm.GoalId = 0;
            vm.isAddUserGoal= true;
        },
        DeleteUserGoal: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Goal")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "USER",
                        refCode: "TALYGEN_PR_goal"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {                           
                            vm.FetchRecords();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Goal')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Goal')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('Goal')]), "failure", true, vm.$t('Alert'));
            }
        },
        DeleteGoal: function (goalId) {            
            var vm = this;
            var val = goalId;
            if (val > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Goal")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "USER",
                        refCode: "TALYGEN_PR_goal"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchRecords();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Goal')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Goal')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },    
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchRecords: async function () {
            var vueObj = this;
            vueObj.UserGoalListing = [];
            var url = `id=${vueObj.UserId}&source=USER_PROFILE&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&lang=EN`;
            await DataService.GetUserGoalList(url).then(function (response) {
                vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'STATUS_ID') {
                            column.settings = {
                                isInSlot: true
                            }
                    }
                    if (column.COLUMN_NAME == 'PROGRESS') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'STARTDATE') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'ENDDATE') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'TITLE') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    });
                if (response.data != "" && response.data != null) {
                    if (response.data.Result.data.length > 0) {                        
                        vueObj.UserGoalListing = response.data.Result.data;
                        vueObj.TotalRecords = response.data.Result.data[0].TOTAL_RECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;                     
                        vueObj.TotalRecords = 0   
                        vueObj.UserGoalListing=[];                  
                    }
                }
                else {
                    vueObj.noRecord = true;                                
                    vueObj.TotalRecords = 0
                    vueObj.UserGoalListing=[];   
                }               
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
       
    },
}
</script>