<template>
<div>
    <div class="listing">
        <div class="expand-list">
            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                <em class="icon-solid arrow-solid-left text-white"></em>
            </a>
        </div>
        <div class="table-responsive table-fix-header" id="div_forListViews">
            <table class="table table-bordered mt-0" id="tblFileList">
                <thead class="thead-bg">
                    <tr>
                        <th class="text-center" style="min-width:5%;">
                            <div class="custom-control custom-checkbox">
                                <input class="chkItems custom-control-input" v-on:click="checkboxChecked(null,'all')" v-model="searchcheck_all" type="checkbox" id="chkedAll_0" name="checkbox">
                                <label class="custom-control-label universal-custom-control-label pt-1" for="chkedAll_0"></label>
                            </div>
                        </th>
                        <!-- <th style="width:50px !important;" class="text-center">
                            <label class="form-check form-check-inline">
                                <div class="custom-control custom-checkbox custom-control-inline">
                                    <input id="chkAllManage" value="" type="checkbox" class="chkItems custom-control-input" v-on:change="onChange('chkAllCheckbox')">
                                    <label for="chkAllManage" class="custom-control-label"></label>
                                </div>
                            </label>
                        </th> -->
                        <th class="text-left" style="min-width:300px;">
                            <span>Feedback Question</span>
                        </th>
                        <th class="text-left">
                            <span>Date</span>
                        </th>
                        <th class="text-left">
                            <span>Created by</span>
                        </th>
                        <th class="text-center">
