                        <th class="text-center">
                            <span>Edit</span>
                        </th>
                        <th class="text-center">
                            <span>View</span>
                        </th>
                    </tr>
                </thead>
                <!-- <tbody v-if="feedbacklistingdata.data">
                        <tr>
                            <td colspan="5" class="text-center text-danger no_records no-record">
                                No record(s) found
                            </td>
                        </tr>
                    </tbody> -->
                <tbody>
                    <tr v-for="(item, index1) in feedbacklistingdata" :key="index1">

                        <td class="text-center">
                            <div class="custom-control custom-checkbox">
                                <input class="chkiteminner custom-control-input" v-model="item.ischecked" :value="item.feedback_id" v-on:click="checkboxChecked(item, 'single')" :id="'chkAll_'+index1" type="checkbox" />
                                <label class="custom-control-label universal-custom-control-label" :for="'chkAll_'+index1"></label>
                            </div>
                        </td>

                        <!-- 
                        <td style="width:50px !important;" class="text-center">
                            <label class="form-check form-check-inline">
                                <div class="custom-control custom-checkbox custom-control-inline">
                                    <input id="chkAll" type="checkbox" class="chkiteminner custom-control-input" :value="item.feedback_id" v-on:change="onChange('innerChkBoxes')">
                                    <label for="chkAll" class="custom-control-label"></label>
                                </div>
                            </label>
                        </td> -->

                        <td class="text-left" style="width: 60px;">
                            <span class="text-center" :title="item.feedback_question">
                                {{ TruncateStr(item.feedback_question,100) }}
                            </span>
                        </td>
                        <td class="text-left">
                            <span class="text-left">
                                {{ $options.filters.formatDate(item.created_at) }}
                            </span>
                        </td>
                        <td class="text-left">
                            <span class="text-center">
                                {{ item.created_by }}
                            </span>
                        </td>
                        <td class="text-center">
                            <span class="text-center">
                                {{ item.is_anonymous == 1 ? 'Yes' : 'No' }}
                            </span>
                        </td>
                        <td class="text-center">
                            <span class="text-center text-overflow-dynamic-container">
                                <div class="cstm-drop-btn" :item="item.feedback_id">
                                    <select id="crmlistingStatus_span" :value="item.status_id" @change="changestatus(item)" class="w-100" :style="{
                                               'background-color': item.status_id == '1001' ? 'rgb(40, 167, 69)' : 'rgb(215, 36, 53)',
                                               'color': 'rgb(255, 255, 255)'
                                            }">
                                        <option value="1001"> Active </option>
                                        <option value="1002"> Inactive </option>
                                    </select>
                                </div>
                            </span>
                        </td>
                        <!-- You might want to add Edit and View functionality here -->
                        <td class="text-center single-action">
                            <a href="#" @click="editFeedback(item,'editfeedback')">
                                <em class="fa fa-pencil text-success"></em>
                            </a>
                        </td>
                        <td class="text-center single-action">
                            <a href="#" @click="editFeedback(item,'viewdetailfeedback')">
                                <em class="fa fa-eye text-info"></em>
                            </a>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- Pagination section (if applicable) -->
        <Pager v-bind:numberofrecords="TotalRecords" v-on:change="pageSizecommon" prev-text="Prev" first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
    </div>
    <AddfeedbackforUser v-if="edtifeedback" :userId="feedbackfor" :oldFeedbackData="oldFeedbackData" :edtifeedback="edtifeedback" @refreshfeedbacklist="getfedbacklisting"></AddfeedbackforUser>
    <viewDetailfeedback v-if="viewDetailfeedback" :oldFeedbackData="oldFeedbackData"></viewDetailfeedback>

</div>
</template>

<script>
import Pager from '../../components/Common/Pager.vue'
import DataService from '../../services/DataService';
import AddfeedbackforUser from "../../components/Modals/AddfeedbackforUser.vue"
import viewDetailfeedback from "../../components/Modals/UserFeedbackDetail.vue"
export default ({
    components: {
        Pager,
        AddfeedbackforUser,
        viewDetailfeedback
    },
    props: {
        userId: {
            type: Number,
            require: true
        }
    },
    data() {
        return {
            feedbacklistingdata: [],
            oldFeedbackData: [],
            Addfeedback: false,
            feedbackfor: null,
            totalRecords: null,
            is_paged: 1,
            sortBy: null,
            sortExp: null,
            PageSize: 10,
            PageNumber: 1,
            TotalPages: 0,
            TotalRecords: 0,
            viewDetailfeedback: false,
            selectedchekboxid: [],
            editdatavalue: false,
            selectedchekid: [],
            edtifeedback: false,
            searchcheck_all: false,
        }

    },
    created: function () {
        var vm = this;
        vm.getfedbacklisting();
    },
    methods: {
        RemovecselectedRecord: function () { 
            try {      
                var vm = this;
                var val = "";
            $('.chkiteminner.custom-control-input:checked:checked').not("[id^='chkAllManage']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            }) 
                if (val.length > 0) {
                    this.selectedchekboxid = val;
                }
                if (vm.selectedchekboxid != "") {
                    vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Records")), function () {
                        var postJSON = {
                            ids: vm.selectedchekboxid,
                            moduleName: "",
                            subModuleCode: "",
                            refCode: "DELETE_FEEDBACK"
                        };
                        var objectPostString = JSON.stringify(postJSON);
                        DataService.CommonCommonDelete(objectPostString).then(function (response) {
                            if (response.status == 200) {
                                $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                $("#DeleteMultiple").addClass("disabled");
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));
                                vm.getfedbacklisting();
                            } else {
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "Success", true, vm.$t("Alert"));
                            }
                        }, );
                    });
                } else {
                    vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                }
            } catch (error) {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        refreshfeedbacklist () {
           this.getfedbacklisting();
        },
        changestatus(item) {
            var vm = this;
            var statusid = null;
            if (item.status_id == 1001) {
                statusid = 1002
            } else {
                9
                statusid = 1001
            }
            var table = 'TALYGEN_PR_feedback';
            var primaryid = 'feedback_id';
            var params = `id=${item.feedback_id}&statusId=${statusid}&tableName=${table}&primaryKeyColumn=${primaryid}`;
            DataService.UpdateStatus(params).then(function (response) {
                if (response.data == "True") {
                    vm.getfedbacklisting();
                    vm.ShowAlert(vm.$t("StatusUpdate"), 'success', true, vm.$t("Alert"));
                }
            })

        },
        // onChange: function (value) {
        //     debugger;
        //     var val = "";
        //     if (value == 'chkAllCheckbox') {
        //         var array = document.getElementsByClassName('chkItems custom-control-input');
        //         for (var i = 0; i < array.length; i++) {
        //             if (array[i].type == "checkbox") {
        //                 if (array[i].checked) {
        //                     var innerarray = document.getElementsByClassName('chkiteminner custom-control-input');
        //                     for (var i = 0; i < innerarray.length; i++) {
        //                         if (innerarray[i].type == "checkbox") {
        //                             innerarray[i].checked = true;
        //                         }
        //                     }
        //                 } else {
        //                     var innerarray = document.getElementsByClassName('chkiteminner custom-control-input');
        //                     for (var i = 0; i < innerarray.length; i++) {
        //                         if (innerarray[i].type == "checkbox") {
        //                             innerarray[i].checked = false;
        //                         }
        //                     }
        //                 }
        //             }
        //         }
        //     } else if (value == 'innerChkBoxes') {
        //         var array = document.getElementsByClassName('chkiteminner custom-control-input');
        //         var innerarray = document.getElementsByClassName('chkItems custom-control-input');
        //         for (var i = 0; i < array.length; i++) {
        //             if (array[i].type == "checkbox") {
        //                 if (array[i].checked) {
        //                     innerarray[0].checked = true;
        //                 } else {
        //                     innerarray[0].checked = false;
        //                     break;
        //                 }
        //             }
        //         }
        //     }
        //     $('.chkiteminner.custom-control-input:checked:checked').not("[id^='chkAllManage']").each(function () {
        //         if (val.length > 0)
        //             val += ',';
        //         val += $(this).val();
        //     })
        //     if (val.length > 0) {
        //         this.selectedchekboxid = val;
        //     }
        // },
        pagerMethod: function (s) {
            var vm = this;
            vm.PageSize = s.PageSize;
            vm.PageNumber = s.PageNumber;
            vm.getfedbacklisting();
        },
        pageSizecommon() {},
        closaddfeedbackpopup() {
            this.Addfeedback = false;
            this.edtifeedback = false;
        },
        getfedbacklisting() {
            var vm = this;
            var params = `userId=${vm.userId}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&is_page=${vm.is_paged}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`;
            DataService.Getfeedbackuserlisting(params).then(function (response) {
                vm.feedbacklistingdata = JSON.parse(response.data[0]).data;
                if (vm.feedbacklistingdata.length > 0) {
                    vm.TotalRecords = vm.feedbacklistingdata[0].TOTAL_RECORDS;
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tblFileList')
                }, 1000);
            })
        },
        editFeedback(value, type) {
            var vm = this;
            var FeedbackId = `feedback_id=${value.feedback_id}`
            DataService.getfedbackdata(FeedbackId).then(function (response) {
                if (response.data) {
                    vm.oldFeedbackData = response.data;
                    vm.feedbackfor = vm.oldFeedbackData.feedback_for;
                    if (type != 'viewdetailfeedback') {
                        vm.edtifeedback = true;
                    } else {
                        vm.viewDetailfeedback = true;
                    }

                }
            });

        },
        checkboxChecked(itemOrType, type) {
            if (type === 'all') {
                if ($('#chkedAll_0').prop('checked')) {
                    this.feedbacklistingdata.forEach(item => {
                        item.ischecked = true;
                        this.searchcheck_all = true;
                    });

                } else {
                    this.feedbacklistingdata.forEach(item => {
                        item.ischecked = false;
                        this.searchcheck_all = false;
                    });
                }
            } else if (type === 'single') {
                // Toggle individual checkbox
                itemOrType.ischecked = !itemOrType.ischecked;
            }
            this.updateSelectAllState(type);
        },

        updateSelectAllState(type) {
            // Check if all feedback items are selected
            if (type === 'single') {
                const allChecked = this.feedbacklistingdata.every(item => item.ischecked);
                this.searchcheck_all = allChecked;
            } else {
                if (this.searchcheck_all) {
                    var innerarray = document.getElementsByClassName('chkiteminner custom-control-input');
                    for (var i = 0; i < innerarray.length; i++) {
                        if (innerarray[i].type == "checkbox") {
                            innerarray[i].checked = true;
                        }
                    }
                }
            }

        },
    }
});
</script>
