<template>
    <section>
        <tg-list ModuleName="User" SubModuleCode="Note"
                                    IdentityColumn="NOTE_ID" :listType="['List']" :ListData="NoteData"
                                    :HeaderData="Headers" 
                                    :TotalRecords="TotalRecords"
                                    :IsShowAction="true" 
                                    :RenderRowActionMethod="renderActions"
                                    v-on:ActionButtonClick="actionButtonClick"
                                    :showCheckBox="true"
                                    :ListDataCallBackFunction="FetchData"
                                    :NorecordfoundText="$t('NoRecordfound')"
                                     v-on:PagerButtonClick="pagerMethod" :HeaderButtons="HeaderButtons"
                                      v-on:SortdataButtonClick="sortdata" v-on:HeaderButtonClick="commonHeaderButtonClick">
                                    <template v-slot:slotdata="{ data }">
                                        <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                                            <listing-status :isfor="'NOTE'" v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')"
                                                ref="updatestatus" v-bind:dataid="data.row.NOTE_ID" data-controller="Crm"
                                                v-bind:statuslist="StatusListForListing" v-bind:item="data.STATUS_ID" data-action="index"
                                                v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                                                v-bind:title ="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                                                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                                            </listing-status>
                                            <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')">
                                                <span v-bind:class="getStatusClass(data.row.STATUS_ID, 'STATUS_ID')">
                                                    {{ data.row[data.column.COLUMN_NAME] }}
                                                </span>
                                            </a>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'TITLE'">
                                            <a href="javascript:;" v-on:click="EditNotespopup(data.row)" ><i
                                                aria-hidden="true" ></i>{{
                                                    data.row.TITLE }}
                                            </a>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'CREATED_AT'">
                                            <span>
                                                {{$options.filters.formatDate(data.row.CREATED_AT) }}
                                            </span>
                                        </template>
                                        <template v-if="data.column.COLUMN_NAME == 'NOTEACCESSIBILITY'">
                                            <span v-if="data.row.NOTEACCESSIBILITY == true">
                                                {{ $t('Public') }} 
                                            </span>
                                            <span v-else-if="data.row.NOTEACCESSIBILITY == false">
                                                {{ $t('Private') }} 
                                            </span>
                                        </template>
                                    </template>
        </tg-list>
        <AddNote v-if="editNotespopup" :UserId="UserId" :NoteId="NoteId" :RefCode="RefCode" :NoteItem="NoteItem" :callbackfunction="FetchData" addUserGuide="USER_GUIDE_USER_NOTE"></AddNote>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import AddNote from '../Modals/AddNote.vue'
export default ({
    name: "ManageNote",
    components:{
    AddNote
    },
    props: {
        UserId: {
            type: Number,
            require: true
        },
    },
    data() {
        return {
            noRecords: false,
            isLoading: false,
            NoteId: 0,
            search: '',
            NoteData: [],
            Headers: [{
                "COLUMN_NAME": "TITLE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Title",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Date",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "USER_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Note Given By",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "NOTEACCESSIBILITY",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Accessibility",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5
            }
            ],
            HeaderButtons :
            [
                {
                    title: 'Add Note',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'openAddNote',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true,
                    id: 'AddNote',
                },
                {
                    title: 'Delete Note',
                    iconClass: 'fa-trash-o',
                    callbackfunction: 'commonDeleteData',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: false,
                    id: 'DeleteMultiple',
                    isEnabledOnSelectedRow: true
                }
            ],
            isLoading: false,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            PageNumber: 1,
            PageSize: 10,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            selectAll: false,
            listActions:[   
                {
                    title: 'Delete',
                    href: 'javascript:void(0)',
                    icon: 'fa fa-trash',
                    callbackfunction: 'DeleteNote',
                    additionalSpan: ''
                }
            ],
            SortExp: '',
            SortBy: '',
            selectedchekboxid:[],
            NoteItem:[],
            RefCode:'',
            editNotespopup:false
        }
    },
    updated(){
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t("Note");
            divElement[0].appendChild(spanElement);
        }
    },
    created: function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {
        EditNotespopup(item){
         this.NoteItem=item;
         this.NoteId = item.NOTE_ID;
         this.editNotespopup=true;
         this.RefCode="Update";
        },
        openAddNote:function(){
            var vm=this;
            vm.RefCode ='Insert';
            vm.NoteId = 0;
            vm.editNotespopup= true;
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;            
            var params = `pageNum=${vm.PageNumber}&pageSize=${vm.PageSize}&sortBy=&sortExp=&userId=${vm.UserId}&search=${vm.search}`;
            DataService.GetUserNoteList(params).then(function (response) { 
                vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'STATUS_ID') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'TITLE') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'CREATED_AT') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'NOTEACCESSIBILITY') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        
                    });     
                if (response.data != "" && response.data != null) {
                    if (response.data.length > 0) {                        
                        vm.NoteData = response.data;
                        vm.TotalRecords = response.data[0].TOTAL_RECORDS;
                        vm.TotalPages = Math.ceil(vmTotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;                     
                        vm.TotalRecords = 0   
                        vm.NoteData =[];                  
                    }
                }
                else {
                    vm.noRecord = true;                                
                    vm.TotalRecords = 0
                    vm.NoteData=[];   
                }               
                setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
                
            })
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteUserNote();
                    break;
                case "openAddNote":
                    this.openAddNote();
                    break;
            }
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event,isChild) {            
            switch (callbackfunction) {
                case 'DeleteNote':
                     this.DeleteNote(item.NOTE_ID);
                    break;
            }
        },
        DeleteUserNote: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Note")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "USER",
                        refCode: "TALYGEN_PR_NOTE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {                           
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Note')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Note')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('Note')]), "failure", true, vm.$t('Alert'));
            }
        },
        DeleteNote: function (noteId) {            
            var vm = this;
            var val = noteId;
            if (val > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Note")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "USER",
                        refCode: "TALYGEN_PR_NOTE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Note')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Note')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "warning", true, vm.$t("Alert"));
            }
        },    
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
    }

});
</script>
