<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel py-2">
            <dynamic-form lang="en" ref="refAssignTeam" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-SearchAndReset="{ data }">
                    <div class="row flex-row-reverse"> 
                        <div class="col-lg-6 text-right">
                            <a id="Search" href="javascript:;" class="btn btn-success mr-1 px-4"
                                title="Click here to search" v-on:click="SearchLocationAndDepartment"><i
                                    class="fa fa-search"></i></a>
                            <a id="ResetUserFilters" href="javascript:;" class="btn btn-danger mr-1 px-4"
                                title="Click here to reset" v-on:click="ResetAll"><i class="fa fa-refresh"></i></a>
                        </div>
                    </div>
                </template>
            </dynamic-form>
            <div class="col-lg-12 mb-3 mt-3">
                <div class="heading-border">
                    <h5 class="h5">
                        <span>User Listing</span>
                        <small class="float-right">
                            <a id="aAddUsers" href="javascript:;" class="table-head-btn icon_add  assntm heighttt"
                                title="Assign Team" v-on:click="AddAssignTeam"><i class="fa fa-plus"></i>Assign Team</a>
                        </small>
                    </h5>
                </div>
            </div>
            <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="Company" SubModuleCode="Cmp_user"
                IdentityColumn="USER_ID" :HeaderText="''" :ListData="GetUserListingAccessTeam" :HeaderData="Headers"
                :TotalRecords="0" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                :IsFilterApplied="searchCondition ? true : false" :LegendArray="[]" sourcetype=""
                v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata"
                :NorecordfoundText="$t('NoRecordfound')">
                <template v-slot:slotdata="{ data }"> 
                    <template v-if="data.column.COLUMN_NAME == 'USER_EMP_ID'">
                        {{ data.row.USER_EMP_ID != null ? data.row.USER_EMP_ID : 'N/A' }}
                    </template>
                </template>
            </tg-list>
        </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';

export default {
    props: {
        UserId: {
            type: Number
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: "",
                            mode: "tag",
                            placeholder: "Enter Title",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                        },
                        {
                            astype: "CustomSelectField",
                            label: this.$t('Department'),
                            name: "Department",
                            value: "",
                            mode: "tag",
                            placeholder: "Enter Department",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Username'),
                            name: "Username",
                            value: "",
                            placeholder: "Enter User Name",
                            visibility: true,
                            config: {
                                showAddIcon: false,
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: '',
                            name: "SearchAndReset",
                            value: "",
                            placeholder: "",
                            visibility: true,
                        },
                    ]
                },
            ],
            Headers: [
                {
                    "COLUMN_NAME": "USER_NAME",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "User Name",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "EMAIL_ID",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Email",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "DEPARTMENT_NAME",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Department Name",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "USER_EMP_ID",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Employee Id",
                    "DISPLAY_ORDER": 4
                },
            ]
        }
    },
    async created() {
        var vm = this;
        await vm.SearchLocationAndDepartment();
        await vm.GetCompanyLocation();
        await vm.GetDepartmentListByCompanyId();
    },
    methods: {
        renderActions() { },
        leftCommonSearch() { },
        commonHeaderButtonClick() { },
        actionButtonClick() { },
        sortdata() { },
        AddAssignTeam(){
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) val += ",";
                val += $(this).val()
            });  
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("AddtionalPhone")), function () {
                    var params = `ids=${val}&parentId=${vm.UserId}`;
                    DataService.AddSearchUser(params).then(response=>{
                        if(response.data.status == 0){
                            vm.ShowAlert(vm.$t('UserAdded'), 'success');
                            $('.chkItems:checkbox:checked,#chkAll').prop("checked", false).removeAttr('checked');
                        }else{
                            vm.ShowAlert(vm.$t('Something_Wrong'), 'failure');
                            $('.chkItems:checkbox:checked,#chkAll').prop("checked", false).removeAttr('checked');
                        }
                    }).catch(err=>{
                        console.log(err);
                    })
                })
            }
        },
        async ResetAll() {
            var vm = this;
            vm.FormSchema[0].Data[0].value = "";
            vm.FormSchema[0].Data[1].value = "";
            vm.FormSchema[0].Data[2].value = "";
            await vm.GetCompanyLocation();
            await vm.GetDepartmentListByCompanyId();
            await vm.SearchLocationAndDepartment();
        },
        async GetCompanyLocation() {
            var vm = this;
            vm.isLoading = true;
            vm.FormSchema[0].Data[0].config.options = [];
            await DataService.GetCompanyLocation().then(response => {
                if (response.data.length > 0) {
                    response.data.forEach(item => {
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: item.LOCATION_NAME,
                            value: item.COMPANY_LOCATION_ID
                        })
                    })
                }
                vm.isLoading = false;
            }).catch(err => {
                console.log(err);
            })
        },
        async GetDepartmentListByCompanyId() {
            var vm = this;
            vm.isLoading = true;
            vm.FormSchema[0].Data[1].config.options = [];
            var includeInactiveStatus = true
            var isTicketHandler = false;
            var params = `includeInactiveStatus=${includeInactiveStatus}&isTicketHandler=${isTicketHandler}`;
            await DataService.GetDepartmentListByCompanyId(params).then(response => {
                if (response.data.length > 0) {
                    response.data.forEach(item => {
                        vm.FormSchema[0].Data[1].config.options.push({
                            name: item.department_name,
                            value: item.department_id
                        })
                    })
                }
                vm.isLoading = false;
            }).catch(err => {
                console.log(err);
            })
        },
        SearchLocationAndDepartment() {
            var vm = this;
            vm.isLoading = true;
            var getdepartment = [];
            var getlocation = [];
            if (vm.FormSchema[0].Data[0].value.length > 0) {
                vm.FormSchema[0].Data[0].value.forEach(item => {
                    getlocation.push(item.value);
                })
                var locationid = getlocation.join(",");
            }
            if (vm.FormSchema[0].Data[1].value.length > 0) {
                vm.FormSchema[0].Data[1].value.forEach(item => {
                    getdepartment.push(item.value);
                })
                var departmentid = getdepartment.join(",");
            }
            var params = `username=${vm.FormSchema[0].Data[2].value}&departmentId=${departmentid == undefined ? "" : departmentid}&locationid=${locationid == undefined ? "" : locationid}&userId=${vm.UserId}`;
            DataService.GetAssignTeam(params).then(response => {
                if (response.data.length > 0) {
                    vm.GetUserListingAccessTeam = response.data;
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'USER_EMP_ID') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    })
                }else{vm.GetUserListingAccessTeam = [];}
                vm.isLoading = false;
            }).catch(err => {
                console.log(err);
            })
        },
    }
}
</script>