<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">

                    <h4 v-if="Addfeedback" name="header" class="modal-title">
                        {{$t('AddFeedback')}}
                    </h4>
                    <h4 v-if="edtifeedback" name="header" class="modal-title">
                        {{$t('UpdateFeedback')}}
                    </h4>
                    <button type="button" class="close" @click=closePopup()>
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>

                </div>
                <div class="modal-body">
                    <dynamic-form ref="dynamicForm" class="daterangecontrolHS" lang="en" :schema="FormSchema">
                    </dynamic-form>
                    <!-- <div class="col-lg-6">
                            <div class="py-2"><span class="mandatory">{{$t('Mandatory')}}</span></div>
                        </div> -->
                </div>
                <div id="divInviteMembers" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2">
                    <div class="listing">
                        <div class="table-responsive table-fix-header float-left" style="max-height:350px;">
                            <table id="tblMembers" class="table table-bordered dt-responsive nowrap mt-0">
                                <thead class="thead-bg">
                                    <tr class="search">
                                        <th style="width:50px !important;" class="text-center">
                                            <label class="form-check form-check-inline">
                                                <div class="custom-control custom-checkbox custom-control-inline">
                                                    <!-- <input id="chkAllManage1" value="" type="checkbox" class="chkItems custom-control-input" v-on:change="onChange('chkAllCheckbox')"> -->
                                                    <input id="chkAllManage1" type="checkbox" class="chkItems custom-control-input" :checked="selectedUserIds.length === ListFeedBackData.data.length" @change="onChange('chkAllCheckbox')" />
                                                    <label for="chkAllManage1" class="custom-control-label"></label>
                                                </div>
                                            </label>
                                        <th>
                                            User Name
                                            <input type="text" class="form-control search" @keyup="ListFilteringusername" id="SearhField" placeholder="Search By User Name" />
                                        </th>
                                        <th id="th-EMAIL">
                                            {{$t('Email')}}
                                            <input type="text" class="form-control search" @keyup="ListFilteringEmail" id="SearhFieldemail" placeholder="Search By Email" />
                                        </th>
                                        <th id="th-DESIGNATION">
                                            {{$t('Designation')}}
                                            <input class="form-control search" type="text" @keyup="ListFilteringDesignation" id="SearhFieldDesignation" placeholder="Search By Designation">
                                        </th>
                                        <th id="th-DEPARTMENT">
                                            {{$t('Department')}}
                                            <input type="text" class="form-control search" @keyup="ListFilteringDepartment" id="SearhFielddepartment" placeholder="Search By Department" />
                                        </th>
                                    </tr>
                                </thead>
                                <tbody v-if="searchfound==true">
                                    <tr>
                                        <td colspan="5" class="text-center text-danger no_records no-record">
                                            No record(s) found
                                        </td>
                                    </tr>
                                </tbody>
                                <tbody id="tblUserList" v-else>
                                    <tr v-for="(item, index) in searchColumnfilter.length != 0 ? searchColumnfilter : ListFeedBackData.data " :key="item.user_id">
                                        <td style="width:50px !important;" class="text-center">
                                            <label class="form-check form-check-inline">
                                                <div class="custom-control custom-checkbox custom-control-inline">
                                                    <input :id="'chkAll'+index" type="checkbox" class="chkinneritem custom-control-input" :checked="selectedUserIds.find(x=> x == item.user_id)" :value="item.user_id" v-on:change="onChange('innerChkBoxes')" />
                                                    <label :for="'chkAll'+index" class="custom-control-label"></label>
                                                </div>
                                            </label>
                                        </td>
                                        <td>
                                            <span searchtext="provider contact lasy">{{ item.username }}</span>
                                        </td>
                                        <td>
                                            <span searchtext="lasy@email.com">{{ item.email_id }}</span>
                                        </td>
                                        <td>
                                            <span searchtext="--">{{item.designation_name}}</span>
                                        </td>
                                        <td>
                                            <span searchtext="--">{{ item.department_name }}</span>
                                        </td>

                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <div class="col-lg-12 my-3">
                    <dynamic-form ref="dynamicForm" class="daterangecontrolHS" lang="en" :mandatorytext="$t('MandatoryString')" :buttons="buttons" @OnSubmit="onSubmit"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../services/DataService';
export default ({

    props: {
        userId: {
            type: Number,
            require: true
        },
        oldFeedbackData: {
            type: Object,
            require: true
        },
        edtifeedback: {
            type: Boolean,
            require: true
        },
        Addfeedback: {
            type: Boolean,
            require: true
        }
    },
    data() {
        return {
            //selectAll: false,
            fileData: [],
            editor: this.CLASSIC_EDITOR,
            department: {
                name: '',
                status: {
                    name: "active",
                    id: 1
                },
                visibaleToclients: false,
                visibaleToTicting: false,
                description: '',
            },
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
            ],
            FormSchema: [{
                    layoutType: 'single',
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Feedback Question'),
                        name: "FeedbackQuestion",
                        value: "",
                        visibility: true,
                        validationRules: {
                            "required": true,
                            max: 1000
                        },
                        config: {
                            options: [],
                            showAddIcon: false,
                        }

                    }]
                },
                {
                    layoutType: 'triple',
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: 1001,
                            visibility: true,
                            validationRules: {
                                "required": true
                            },
                            config: {
                                options: [{
                                    name: 'Active',
                                    value: 1001
                                }, {
                                    name: 'InActive',
                                    value: 1002
                                }],
                                showAddIcon: false,
                            }
                        },
                        {
                            astype: "ToggleField",
                            label: this.$t('Is Anonymous'),
                            name: "IsAnonymous",
                            value: "",
                            visibility: true,
                            config: {
                                options: [{
                                    name: 'Yes',
                                    value: 1
                                }, {
                                    name: 'No',
                                    value: 2
                                }],
                                showAddIcon: false,
                            }
                        },
                        {
                            astype: "RadioField",
                            label: "",
                            name: "Type",
                            value: 1,
                            placeholder: "",
                            config: {
                                options: [{
                                    name: 'Private',
                                    value: 0
                                }, {
                                    name: 'Public',
                                    value: 1
                                }],
                                showAddIcon: false,
                            },
                        }
                    ]
                }
            ],
            OtherSchema: [],
            isLoading: false,
            showContractDiv: false,
            ManageQuestionaire: false,
            modelStars: [],
            GetCheckedValue: null,
            modelQuestionVisibility: [],
            GetReviewQuestionList: [],
            RadioOptions: [{
                label: 'Enable',
                value: 0
            }, {
                label: 'Disable',
                value: 1
            }],
            RadioVisibilityOtions: [{
                label: 'Both',
                value: 2
            }, {
                label: 'Candidate',
                value: 3
            }, {
                label: 'Reviewer',
                value: 4
            }],
            FeedbackColumnArray: [{
                FeedbackQuestion: "",
                FeedbackAnswerType: "",
                FeedbackIndex: 0,
                FeedbackAnsweringType: '',
                FeedbackAnsweringValue: ''
            }],
            count: 0,
            skill: '',
            notes: '',
            feedback: '',
            goal: '',
            training: '',
            male: '',
            female: '',
            FeedbackAnsweringValue: '',
            ListFeedBackData: [],
            selectedUserIds: this.oldFeedbackData && this.oldFeedbackData.FeedbackUserIDs ?
                this.oldFeedbackData.FeedbackUserIDs.split(',').map(id => id.trim()) :
                [],
            searchusername: '',
            searchemail: '',
            searchDesignation: '',
            searchDepartment: '',
            searchfound: false,
            searchColumnfilter: [],
            SelectedFeedBackIds: []
        }
    },

    created: function () {
        var vm = this;
        if (vm.oldFeedbackData != null || vm.oldFeedbackData != undefined) {
            vm.updatefeedbackvalue(vm.oldFeedbackData);
        }
        vm.getuserlist()
    },
    methods: {
        ListFilteringusername: function () {
            var vm = this;
            document.querySelector('#chkAllManage1').checked = false;
            vm.onChange('innerChkBoxes');
            var input = document.getElementById('SearhField');
            var filterInput = input.value.toLowerCase();
            var filteredData = vm.ListFeedBackData.data.filter(item => {
                var displayMainColname = item.username;
                return displayMainColname.toLowerCase().includes(filterInput);
            });
            vm.searchColumnfilter = filteredData;
            vm.searchfound = filteredData.length === 0;
            return vm.searchColumnfilter;
        },
        ListFilteringEmail() {
            var vm = this;
            document.querySelector('#chkAllManage1').checked = false;
            vm.onChange('innerChkBoxes');
            var input = document.getElementById('SearhFieldemail');
            var filterInput = input.value.toLowerCase();
            var filteredData = vm.ListFeedBackData.data.filter(item => {
                var displayMainColname = item.email_id;
                return displayMainColname.toLowerCase().includes(filterInput);
            });
            vm.searchColumnfilter = filteredData;
            vm.searchfound = filteredData.length === 0;
            return vm.searchColumnfilter;
        },

        ListFilteringDesignation() {
            var vm = this;
            document.querySelector('#chkAllManage1').checked = false;
            vm.onChange('innerChkBoxes');
            var input = document.getElementById('SearhFieldDesignation');
            var filterInput = input.value.toLowerCase();
            var filteredData = vm.ListFeedBackData.data.filter(item => {
                var displayMainColname = item.designation_name;
                return displayMainColname.toLowerCase().includes(filterInput);
            });
            vm.searchColumnfilter = filteredData;
            vm.searchfound = filteredData.length === 0;
            return vm.searchColumnfilter;
        },

        ListFilteringDepartment() {
            var vm = this;
            document.querySelector('#chkAllManage1').checked = false;
            vm.onChange('innerChkBoxes');
            var input = document.getElementById('SearhFielddepartment');
            var filterInput = input.value.toLowerCase();
            var filteredData = vm.ListFeedBackData.data.filter(item => {
                var displayMainColname = item.department_name;
                return displayMainColname.toLowerCase().includes(filterInput);
            });
            vm.searchColumnfilter = filteredData;
            vm.searchfound = filteredData.length === 0;
            return vm.searchColumnfilter;
        },

        updatefeedbackvalue(olddata) {
            var vm = this;
            vm.getuserlist();
            vm.FormSchema[0].Data[0].value = olddata.feedback_question;
            vm.FormSchema[1].Data[0].value = olddata.status_id;
            vm.FormSchema[1].Data[1].value = olddata.is_anonymous;
            vm.FormSchema[1].Data[2].value = olddata.is_shared;
            if (typeof olddata.FeedbackUserIDs === 'string') {
                vm.selectedUserIds = olddata.FeedbackUserIDs.split(',').map(id => id.trim());
            } else {
                vm.selectedUserIds = olddata.FeedbackUserIDs;
            }
        },
        closePopup() {
            this.$parent.closaddfeedbackpopup();
        },
        getuserlist() {
            var vm = this;
            var params = `FeedBackUserid=${vm.userId}`;
            DataService.GetUserfeedback(params).then(function (response) {
                var listdata = JSON.parse(response.data[0]);
                if (listdata != null || listdata != undefined) {
                    vm.ListFeedBackData = listdata;
                }
            });
        },
        onChange(value) {
            if (value === 'chkAllCheckbox') {
                const allChecked = document.querySelector('#chkAllManage1').checked;
                this.selectedUserIds = allChecked ?
                    this.ListFeedBackData.data.map(item => item.user_id) :
                    [];
                this.SelectedFeedBackIds = [];
            } else if (value === 'innerChkBoxes') {
                this.selectedUserIds = Array.from(document.querySelectorAll('.chkinneritem.custom-control-input:checked'))
                    .map(checkbox => checkbox.value);
                const combined = [...this.SelectedFeedBackIds || [], ...this.selectedUserIds];
                this.SelectedFeedBackIds = [...new Set(combined)];
            }
            if (this.SelectedFeedBackIds.length > 0) {
                setTimeout(() => {
                    this.SelectedFeedBackIds.forEach(id => {
                        const checkbox = document.querySelector(`.chkinneritem.custom-control-input[value="${id}"]`);
                        if (checkbox) {
                            checkbox.checked = true;
                        }
                    });
                }, 300);
            } else {
                this.SelectedFeedBackIds = [];
                console.log('Selected User IDs:', this.selectedUserIds);
                const allChecked = this.ListFeedBackData.data.every(item => this.selectedUserIds.includes(item.user_id));
                document.querySelector('#chkAllManage1').checked = allChecked;
                console.log('All Checked:', allChecked);
            }
            
        },

        async onSubmit() {
            var ths = this;
            if (ths.selectedUserIds == "") {
                ths.ShowAlert("Please select at least one user from user list.", "warning", true, ths.$t("Alert"));
            } else {
                ths.selectedUserIds = ths.selectedUserIds.join(',');
            }
            if (ths.FormSchema[0].Data[0].value == "" && ths.selectedUserIds != "") {
                ths.ShowAlert("Please Enter Feedback Question", "warning", true, ths.$t("Alert"));
            }
            var feedbackid = null;
            var anonumous = null;
            if (ths.oldFeedbackData === undefined) {
                feedbackid = 0;
            } else {
                feedbackid = ths.oldFeedbackData.feedback_id;
            }
            if (ths.FormSchema[1].Data[1].value == true) {
                anonumous = 1;
            } else {
                anonumous = 0;
            }
            var fieldData = {
                feedbackquestion: ths.FormSchema[0].Data[0].value,
                isanonymous: anonumous,
                statusid: ths.FormSchema[1].Data[0].value,
                Accessibility: ths.FormSchema[1].Data[2].value,
                feedback_for: ths.userId,
                feedback_id: feedbackid,
                is_shared: ths.FormSchema[1].Data[2].value,
                user_ids: ths.oldFeedbackData === undefined ? JSON.stringify(ths.selectedUserIds).replace('[', '').replace(']', '') : ths.selectedUserIds,
            }
            var obj = JSON.stringify(fieldData);
            var Data = {
                objData: obj
            };
            if (ths.FormSchema[0].Data[0].value != "" && ths.selectedUserIds != "") {
                DataService.Addeditfeedbackdata(Data).then((res) => {
                    if (res.data == 1) {
                        ths.closePopup();
                        this.$emit('refreshfeedbacklist');
                        ths.ShowAlert("Feedback has been Added Successfully", "Success", true, ths.$t("Alert"));
                    } else {
                        ths.closePopup();
                        this.$emit('refreshfeedbacklist');
                        ths.ShowAlert(ths.$t('UpdatedSuccessfully'), 'Success', true, ths.$t("Alert"));
                    }
                });
            }
        },
    },

});
</script>
