<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="padding-t_10 p-0" id="reportstabsdata">
            <div class="left-menu-tab pt-2">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ Headerstext }} </span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab  nav-tabs">
                    <li v-for="tab in tabs" :key="tab.id" @click="activeTab = tab.id, changeTab(tab.id)"
                        :class="{ 'nav-item': true, 'active': activeTab === tab.id }">
                        <a :class="{ 'nav-link left-menu': true, 'active': activeTab === tab.id }" :title="tab.title">
                            <em :class="tab.icon" class="p-1"  ></em> {{ tab.title }}</a>
                    </li>
                </ul>

            </div>


            <div class="right-content">

                <div id="ReturnRequestHeading" class=" mt-2 theme-primary partition  p-actions-expand"
                    v-if="tabHeaderText !== 'Skill' && tabHeaderText !== 'Goal' && tabHeaderText !== 'Notes'">
                    <span class="p-name text-white"> {{ tabHeaderText }}</span>

                    <span v-if="tabHeaderText == 'Team Structure'" class="user-guide float-right m-2">
                        <a class="ancuserguide text-white" data-toggle="collapse"  role="button"
                            aria-expanded="false" aria-controls="collapseExample"> <em
                                ></em>User Guide</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('TeamStructureUserGuide')"></div>
                            </div>
                        </div>
                    </span>


                    <span v-if="tabHeaderText === 'Feedback'">
                        <a id="addnewreview" href="javascript:;" title="Add Feedback" class="p-action-btn text-white float-right"
                            @click="openAddfeedbackpopup" @tooltip="showTooltip('Add Feedback')">
                            <i class="fa fa-plus"></i>
                            <br>
                            <span>Add Feedback</span>
                        </a>
                        <a id="addnewreview" href="javascript:;" title="Remove" class="p-action-btn text-white float-right"
                            @click="RemovecselectedRecord"
                            @tooltip="showTooltip('Please select one/multiple check box(s) to remove review(s)')">
                            <i class="fa fa-trash"></i>
                            <br>
                            <span>Remove</span>
                        </a>
                    </span>
                    <!-- <span v-if="tabHeaderText === 'Notes'">
                        <a id="addnewnote" href="javascript:;" title="Add Notes" class="p-action-btn text-white float-right"
                            @click="AddReview" @tooltip="showTooltip('Add Notes')">

                            <i class="fa fa-plus"></i>
                            <br>
                            <span>Add Note</span>
                        </a>
                        <a :class="{ 'p-action-btn text-white float-right iconenable': true, 'disabled': IsDisabled }"
                            href="javascript:void(0);" @click="RemoveNoteRecord" title="Remove" id="DeleteMultiple_New"
                            @tooltip="showTooltip('Please select one/multiple check box(s) to remove review(s)')">
                            <i class="fa fa-trash"></i>
                            <br>
                            <span>Remove</span>
                        </a>
                    </span> -->
                    <span v-if="tabHeaderText === 'Performance Review'">
                        <a :class="{ 'p-action-btn text-white float-right iconenable': true, 'disabled': !isDeleteMultiple }"
                            @click="RemoveReview" href="javascript:void(0);" :title="$('Remove')" id="DeleteMultiple_New"
                            @tooltip="showTooltip('Please select one/multiple check box(s) to remove review(s)')">
                            <i class="fa fa-trash"></i>
                            <br>
                            <span>{{ $t('Remove') }}</span>
                        </a>

                        <a id="addnewreview" href="javascript:;" :title="$t('AddReview')" class="p-action-btn text-white float-right"
                            @click="OpenAddReview" @tooltip="showTooltip('Add Review')">
                            <i class="fa fa-plus"></i>
                            <br>
                            <span>{{$t('AddReview')}}</span>
                        </a>
                    </span>
                    <!-- <span v-if="tabHeaderText === 'Goal'">
                        <a class="p-action-btn text-white float-right iconenable disabled" href="javascript:void(0);"  @click="AddGoalReview"  title="" id="DeleteMultiple_New" @tooltip="showTooltip('Please select one/multiple check box(s) to remove review(s)')">
                             <i class="fa fa-trash"></i>
                             <br>
                            <span>Remove Goal</span>
                        </a>
                        <a id="addnewreview" class="p-action-btn text-white float-right"
                                    :to="{ name: 'AddGoal' }" title="" @click="AddGoalReview"
                                    v-tooltip="'Add a Goal'">
                            <i class="fa fa-plus"></i>
                            <br>
                            <span>Add a Goal</span>
                        </a>
                    </span> -->


                </div>

                <div class="col-md-12 p-0" v-for="tab in tabs" :key="tab.id" v-show="activeTab === tab.id">

                    <EditUser v-if="isSettingsVisible && tab.id == 1" @userTypeId="receiveUserTypeId"
                        :isEditUser="isEditUser" :UserId="userId">
                    </EditUser>

                    <AssociateLicense v-if="isAssociateVisible && tab.id == 2" :UserId="userId" :userType="userType" :UserTypeName="UserTypeName" :IS_DEFAULT="IS_DEFAULT">
                    </AssociateLicense>

                    <Address v-if="isAddressVisible && tab.id == 3" :UserId="userId" IsClient="User">

                    </Address>

                    <Communication v-if="isCommunicationVisible && tab.id == 4" :GetConfiguration="GetConfiguration" IsClient="User">

                    </Communication>


                    <modalConfiguration v-if="tab.id == 5 && isConfigSelected" :GetConfiguration="GetConfiguration">
                    </modalConfiguration>

                    <NotesVue v-if="isNotesVisible && tab.id == 9" :isEditUser="isEditUser" :UserId="userId">
                    </NotesVue>

                    <ContactNumberVue v-if="isShowContactNumber && tab.id == 6" @userTypeId="receiveUserTypeId"
                        :isEditUser="isEditUser" :UserId="userId" IsClient="User"></ContactNumberVue>

                    <TeamStructure v-if="isTeamStructureVisible && tab.id == 13" :UserId="userId">


                    </TeamStructure>
                    <!-- <NotesVue v-if="isNotesVisible && tab.id == 9" @userTypeId="receiveUserTypeId"
                        :isEditUser="isEditUser" :UserId="userId">
                  </NotesVue> -->

                    <Notification v-if="isNotificationTabVisible && tab.id == 10" :UserId="userId">

                    </Notification>
                    <!-- <AddNotes v-if="showAddNotes" @Cancel="Cancel" :UserId="userId" :NoteItem="NoteItem"
                        addUserGuide="User_Guide_Note"></AddNotes> -->

                    <feedbackuserlisting ref="feedbacklisting" v-if="isFeedbackSettingsVisible && tab.id == 7"
                        :userId="userId">

                    </feedbackuserlisting>
                    <UserBreak v-if="isUserBreak && tab.id == 14" @userTypeId="receiveUserTypeId"
                        :isEditUser="isEditUser" :UserId="userId"></UserBreak>

                    <UserSkill v-if="isSkillVisible && tab.id == 12" :UserId="userId"></UserSkill>
                    <GoalVue v-if="isGoalVisible && tab.id == 8" @userTypeId="receiveUserTypeId"
                        :isEditUser="isEditUser" :UserId="userId">
                    </GoalVue>
                    <!-- <PerformanceReview v-if="isPerformanceReviewVisible && tab.id == 11" :UserId="userId" @DeleteMultiple="DeleteMultiple"></PerformanceReview> -->
                    <PerformanceReview ref="performanceReview" v-if="isPerformanceReviewVisible && tab.id == 11" :UserId="userId"
                        @IsDeleteMultiple="IsDeleteMultiple" :key="PeformanceUpdatekey"></PerformanceReview>
                    <AddPerformanceReview v-if="showAddReview && tab.id == 11" :UserId="userId"></AddPerformanceReview>
                    <AssignTeam v-if="showAssignTeam && tab.id == 15" :UserId="userId"></AssignTeam>
                </div>

                <AddfeedbackforUser v-if="Addfeedback" :userId="userId" :Addfeedback="Addfeedback"  @refreshfeedbacklist="refreshfeedbacklist"></AddfeedbackforUser>
                <!-- <AddNotes v-if="showAddNotes" @Cancel="Cancel"></AddNotes> -->
            </div>
        </div>

    </section>
</template>
<script>
import DataService from '../../services/DataService';
import EditUser from "../Users/AddUser.vue";
import AssociateLicense from "../Users/AssociateLicense.vue";
import Address from "../Users/Address.vue";
import Communication from "./Communication.vue";
import ContactNumberVue from '../../components/Modals/ContactNumber.vue';
import NotesVue from '../../components/Modals/Notes.vue';
import TeamStructure from "./TeamStructure.vue";
import Notification from "./Notification.vue";
// import NotesVue from '../../components/Modals/Notes.vue';
// import AddNotes from "../../components/Modals/AddNote.vue";
import GoalVue from '../../components/Modals/Goal.vue';
import AddfeedbackforUser from "../../components/Modals/AddfeedbackforUser.vue"
//  import AddNote from "../../components/Modals/AddNote.vue"
import feedbackuserlisting from "../../views/Users/userfeedbacklisting.vue"
import modalConfiguration from "../../components/Modals/modalConfiguration.vue";
import UserBreak from "../Users/UserBreak.vue";
import UserSkill from "../Users/UserSkill.vue";
import PerformanceReview from "../../views/Users/PerformanceReviewListing.vue"
import AddPerformanceReview from "../../components/Modals/modalAddPerformanceReview.vue"
import AssignTeam from '../../components/Modals/AssignTeam.vue';
export default {

    components: {
        EditUser,
        AssociateLicense,
        Address,
        Communication,
        ContactNumberVue,
        NotesVue,
        GoalVue,
        TeamStructure,
        Notification,
        // AddNotes,
        UserBreak,
        UserSkill,
        Notification,
        AddfeedbackforUser,
        feedbackuserlisting,
        modalConfiguration,
        PerformanceReview,
        AddPerformanceReview,
        AssignTeam
    },
    props: {
        tabHeaderText: {
            type: String,
            required: true
        }
    },

    data() {
        return {
            Addfeedback: false,
            tabHeaderText: "",
            isSettingsVisible: false,
            isNotesVisible: false,
            isGoalVisible: false,
            isAssociateVisible: false,
            isAddressVisible: false,
            isCommunicationVisible: false,
            isTeamStructureVisible: false,
            isNotificationTabVisible: false,
            isSkillVisible: false,
            isUserBreak: false,
            activeTab: 1,
            isEditUser: '',
            tabs: [{
                    title: "GeneralSettings",
                    icon: "fa fa-cogs",
                    id: 1
                },
                {
                    title: "Associate Licenses",
                    icon: "fa fa-plus",
                    id: 2
                },
                {
                    title: "Addresses",
                    icon: "fa fa-address-book",
                    id: 3
                }],
            userId: null,
            userType: null,
            Headerstext: '',
            showAddNotes: false,
            GetConfiguration: {},
            isConfigSelected: false,
            isPerformanceReviewVisible: false,
            isDeleteMultiple: false,
            showAddReview:false,
            NoteItem:[],
            IsDisabled:true,
            PeformanceUpdatekey:0,
            refreshlisting:false,
            UserTypeName:'',
            ExtraFeature:'NO',
            showAssignTeam:false,
            IS_DEFAULT:false
        }
    },

    async created() {
        var vm = this;
        vm.userId = parseInt(this.$route.params.id);
        vm.isSettingsVisible = true;
        vm.tabHeaderText = 'General Settings';
        vm.isEditUser = "EditUser"
        vm.GetUserDetailByUserId();
        if(vm.userId > 0 && this.GetUserInfo.UserType == 'CA'){
        await vm.GetUserLicenseModules();}
    },

    methods: {
        async CheckExtraFeatures(){
            var vm = this;
            vm.ExtraFeature = 'NO';
            var params = `FeatureCode=CTBREAK`;
            await DataService.CheckExtraFeatures(params).then(response=>{
                if(response.data.status == "Success" && response.data.msg.toUpperCase() == "TRUE"){
                    vm.ExtraFeature = 'YES';
                    vm.tabs.push({
                        title: "Assign Team",
                        icon: "fa fa-users",
                        id: 15
                    })
                }
            }).catch(err=>{
                console.log();
            })
        },
        async GetUserLicenseModules(){
            var vm = this;
            await vm.CheckExtraFeatures();
            vm.tabs.push({
                title: "Communication",
                icon: "fa fa-phone",
                id: 4
            },
            {
                title: "Configuration",
                icon: "fa fa-cog",
                id: 5
            },
            {
                title: "Contact Numbers",
                icon: "fa fa-file",
                id: 6
            });
            await DataService.GetUserLicenseModules().then(response=>{
                if(response.data.length > 0){
                    var getFeedback = response.data.find(x=> x.controllerName.toUpperCase() == 'FEEDBACK');
                    var getGoal = response.data.find(x=> x.controllerName.toUpperCase() == 'GOAL');
                    var getNote = response.data.find(x=> x.controllerName.toUpperCase() == 'NOTE');
                    var getReview = response.data.find(x=> x.controllerName.toUpperCase() == 'REVIEW');
                    var getSkill = response.data.find(x=> x.controllerName.toUpperCase() == 'SKILL');
                    var getTimesheet = response.data.find(x=> x.controllerName.toUpperCase() == 'TIMESHEET');
                    if(getFeedback.controllerName != undefined){
                        vm.tabs.push({
                            title: "Feedback",
                            icon: "fa fa-quote-left",
                            id: 7
                        });
                    }if(getGoal.controllerName != undefined){
                        vm.tabs.push({
                            title: "Goal",
                            icon: "fa fa-trophy",
                            id: 8
                        });
                    }if(getNote.controllerName != undefined){
                        vm.tabs.push({
                            title: "Notes",
                            icon: "fa fa-sticky-note",
                            id: 9
                        });
                    }
                    vm.tabs.push({
                        title: "Notifications",
                        icon: "fa fa-bell",
                        id: 10
                    });
                    if(getReview.controllerName != undefined){
                        vm.tabs.push({
                            title: "Performance Review",
                            icon: "fa fa-file-text",
                            id: 11
                        });
                    }if(getSkill.controllerName != undefined){
                        vm.tabs.push({
                            title: "Skill",
                            icon: "fa fa-star",
                            id: 12
                        });
                    }
                    vm.tabs.push({
                        title: "Team Structure",
                        icon: "fa fa-sitemap",
                        id: 13
                    });
                    if(getTimesheet.controllerName != undefined){
                        vm.tabs.push({
                            title: "User Break",
                            icon: "fa fa-clock-o",
                            id: 14
                        });
                    }
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        refreshfeedbacklist () {
           this.$refs.feedbacklisting[0].getfedbacklisting();
        },
        async GetUserDetailByUserId() {

            var vm = this
            try {
                let url = `userId=${vm.userId}`;
                await DataService.GetUserDetailById(url).then(response => {
                    vm.userType = response.data.UserType;
                    vm.UserTypeName = response.data.USER_TYPE_NAME;
                    vm.userDetail = response.data;
                    let userName = response.data.User_Name.replace(" ", '') + ' ';
                    let designation = response.data.designation + '  , ' || ' ';
                    let department = response.data.department + ' ' || ' ';
                    vm.IS_DEFAULT = response.data.IS_DEFAULT;
                    vm.Headerstext = `${userName}-${designation}${department}`;
                    // if (vm.Headerstext.length > 10) { // Example length check

                    // }
                })
            } catch (error) {
                console.log("error message", error);
            }
        },

        RemovecselectedRecord: function () {
            var vm = this;
            vm.$refs.feedbacklisting[0].RemovecselectedRecord();
        },
        // getselectedvalue(value) {
        //     this.selectedfeedbackdata = value;
        // },
        closaddfeedbackpopup() {
            this.Addfeedback = false;
        },
        deleteReview() {
            console.log('deleteReview called');
        },
        showTooltip(message) {
            console.log('Tooltip:', message);
        },
        Cancel: function () {
            var vm = this;
            vm.showAddNotes = false;
        },
        
        receiveUserTypeId(userTypeid, userName) {
            var vm = this;
            vm.userType = '';
            vm.userType = parseInt(userTypeid);
            vm.Headerstext = userName;
        },

        changeTab(tab) {
            var vm = this;

            this.isSettingsVisible = tab === 1;
            this.isAssociateVisible = tab === 2;
            this.isAddressVisible = tab === 3;
            this.isCommunicationVisible = tab === 4;
            this.isShowContactNumber = tab === 6;
            this.isTeamStructureVisible = tab === 13;
            this.isNotificationTabVisible = tab === 10;
            this.showAssignTeam = tab === 15;

            switch (tab) {

                case 1:
                    vm.isSettingsVisible = true;
                    vm.tabHeaderText = 'GeneralSettings';
                    break;
                case 2:
                    vm.tabHeaderText = 'Associate Licenses';
                    vm.isAssociateVisible = true
                    break;
                case 3:
                    vm.tabHeaderText = 'Addresses';
                    vm.isAddressVisible = true;
                    break;
                case 4:
                    vm.tabHeaderText = 'Communication';
                    vm.isCommunicationVisible = true;
                    vm.GetConfiguration = { user_id: vm.userId, user_name: vm.Headerstext };
                    break;
                case 5:
                    vm.tabHeaderText = 'Configuration';
                    vm.isConfigSelected = true;
                    vm.GetConfiguration = { user_id: vm.userId, user_name: vm.Headerstext, Action: 'Edit' };
                    break;
                case 6:
                    vm.isShowContactNumber = true;
                    vm.tabHeaderText = 'Contact Numbers';
                    break;
                case 7:
                    vm.isFeedbackSettingsVisible = true;
                    vm.tabHeaderText = 'Feedback';
                    break;
                case 8:
                    vm.isGoalVisible = true;
                    vm.tabHeaderText = 'Goal';
                    break;
                case 9:
                    vm.tabHeaderText = 'Notes';
                    vm.isNotesVisible = true;
                    break;
                case 10:
                    vm.tabHeaderText = 'Notifications';
                    vm.isNotificationTabVisible = true;
                    break;
                case 11:
                    vm.tabHeaderText = 'Performance Review';
                    vm.isPerformanceReviewVisible = true;
                    break;
                case 12:
                    vm.tabHeaderText = 'Skill';
                    vm.isSkillVisible = true;
                    break;
                case 13:
                    vm.tabHeaderText = 'Team Structure';
                    vm.isTeamStructureVisible = true;
                    break;
                case 14:
                    vm.isUserBreak = true;
                    vm.tabHeaderText = 'User Break';
                    break;
                case 15:
                    vm.showAssignTeam = true;
                    vm.tabHeaderText = 'Assign Team';
                    break;
                default:
                    break;
            }
        },
        AddReview() {
            var vm = this;
            vm.showAddNotes = true;
        },
        previousTab() {
            if (this.activeTab > 1) {
                this.activeTab--;
            }
        },
        nextTab() {
            if (this.activeTab <= this.tabs.length - 1) {
                this.activeTab++;
            }
        },
        openAddfeedbackpopup() {
            this.Addfeedback = true
        },
        CloseAddNote() {
            this.showAddNotes = false;
            this.changeTab(8);
            setTimeout(() => {
                this.changeTab(9);
            }, 100);
        },
        IsDeleteMultiple(value) {
            var vm = this;
            vm.isDeleteMultiple = value;
        },
        OpenAddReview() {
            var vm = this;
            vm.showAddReview = true;
        },
        CloseAddReview(value){
            var vm  = this;
            vm.showAddReview = false;
            if(value == "Save"){
                vm.isPerformanceReviewVisible = false;
                vm.changeTab(11);
                vm.PeformanceUpdatekey += 1;
            }
        },
        RemoveReview() {
            var vm = this;
            vm.$refs.performanceReview[0].GetReviewData();
        },
        Getselectedval(value, val) {
            this.IsDisabled = val;
            this.selectedNoteId = value;
        },
        RemoveNoteRecord: function () {
            var vm = this;
            vm.isLoading = true;
            if (vm.selectedNoteId != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Records")), function () {

                    var params = `NoteId=${vm.selectedNoteId}&userId=${vm.userId}`;
                    DataService.DeleteUserNotes(params).then(function (response) {

                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));
                            vm.changeTab(8);
                            setTimeout(() => {
                                vm.changeTab(9);
                            }, 100);
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "failure", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
    },

}
</script>
<style></style>