<template lang="en">
<section>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle py-1">
                        <h5 name="header" class="pt-2">
                            {{ $t('Skill') }}
                        </h5>
                        <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form ref="validateManageSkill" class="daterangecontrolHS" lang="en" :buttons="false" :schema="FormSchema">
                        </dynamic-form>
                        <div class="form-group">
                            <label for="transactionSize">Proficiency by Manager: <span style="border: 0; color: #f6931f; font-weight: bold;">{{ proficiencyManager
                                        }}%</span></label>
                            <div class="slider-container pt-3 ml-2">
                                <VueSlider min="0" id="proficiencyManager" @input="onSlide" max="100" v-model="proficiencyManager" />
                            </div>
                        </div>
                        <div class="col-lg-12 p-0">
                            <div id="accordion" class="w-100">
                                <div class="card mb-4" v-for="(groupItem, key, index) in groupByDate">
                                    <div class="card-header">
                                        <a :class="{ 'card-link collapsed': index > 1, 'card-link': index <= 1 }" style="color:#000" data-toggle="collapse" :href="'#collapseOneThread' + index">
                                            <div class="conversation-date heading-border"><span>{{
                                                    headerFormatTime(key) }}</span>
                                            </div>
                                        </a>
                                    </div>
                                    <div>
                                        <div :id="'collapseOneThread' + index" :class="{ 'collapse': index > 1, 'collapse show': index <= 1 }">
                                            <div class="card-body">
                                                <div class="conversation-main">

                                                    <template v-for="(item, index) in groupItem">
                                                        <div class="conversation-time">
                                                            <span>{{ formatTime(item.CREATED_AT) }}</span>
                                                        </div>
                                                        <div class="conversation-box-con">
                                                            <div class="conversation-box">
                                                                <div class="name-circle light-yellow">
                                                                    <img :src="item.AVATAR ? item.AVATAR : '/Content/images/DefaultImages/avatar-icon.jpg'" alt="Image" width="51" height="51" style="font-size:12px" loading="lazy" />
                                                                </div>
                                                                <div class="conversation-header addednote-ticket">
                                                                    <span class="sender-info">
                                                                        <span class="sender-name mr-2">{{
                                                                                item.USER_NAME }}</span>
                                                                        <span class="sender-department mr-2">(
                                                                            {{ $t('PostedOn') }} :</span><span class="sender-department" style="color : #dc3545!important"> {{
                                                                                    toPrettyString(item.CREATED_AT) }}
                                                                        </span> <span class="sender-department">)</span>
                                                                    </span>
                                                                </div>
                                                                <div class="conversation-content addednote-ticket">
                                                                    <p>{{ item.DESCRIPTION }}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </template>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <dynamic-form ref="validateManageComment" lang="en" :buttons="commentButtons" :schema="CommentSchema">
                            </dynamic-form>
                        </div>
                        <div class="modal-footer row" style="border-top: none !important">
                            <a href="javascript:;" class="btn btn-success save-btn" v-on:click="SaveData()" title="Save"><i class="fa fa-floppy-o pr-2"></i>Save</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import {
    defineComponent
} from '@vue/composition-api'
import DataService from '../../services/DataService';
export default defineComponent({
    props: {
        UserId: {
            type: Number,
            required: false
        },
        callbackfunction: {
            type: Function
        },
        UserSkillArray: {
            type: Array,
            required: false
        }
    },
    data() {
        return {
            EmployeeProfiency: 0,
            editor: this.CLASSIC_EDITOR,
            proficiencyManager: 0,
            CommentList: [],
            Avatar: null,
            FormSchema: [{
                layoutType: "double",
                Data: [{
                        astype: "TextField",
                        label: this.$t('Skill'),
                        name: "Skill",
                        value: "",
                        placeholder: "Skill",
                        validationRules: {
                            "required": true

                        },
                        disabled: true,
                    },
                    {
                        astype: "TextField",
                        label: this.$t('EmployeeProfiency'),
                        name: "EmployeeProfiency",
                        value: "",
                        placeholder: "Employee Profiency",
                        validationRules: {
                            "required": true
                        },
                        disabled: true,
                    }
                ],
            }, ],
            CommentSchema: [{
                layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Comment'),
                    name: "Comment",
                    value: "",
                    config: {
                        rows: 3
                    },
                    placeholder: this.$t('Enter Comment'),
                    validationRules: {
                        "required": true,
                        //"regex": /^[a-zA-Z0-9\s]*$/
                    },
                }]
            }],
            commentButtons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-paper-plane pr-2'></i>Post New Comment",
                isVisible: true,
                onClick: this.SaveComment,
            }],
            isLoading: false
        }
    },
    computed: {
        groupByDate() {
            var vm = this;
            let groups = {};
            vm.CommentList.forEach(item => {
                let date = item.CREATED_AT.substring(0, 10);
                if (groups[date]) {
                    groups[date].push(item);
                } else {
                    groups[date] = [item];
                }
            });

            return groups;
        }
    },
    async created() {
        var vm = this;
        if (vm.UserSkillArray.row != null) {
            // vm.$refs.validateManageSkill.updateProperty("Skill", "value", vm.UserSkillArray.row.TITLE);
            // vm.$refs.validateManageSkill.updateProperty("EmployeeProfiency", "value", vm.UserSkillArray.row.SKILL_PROGRESS);
            // vm.$refs.validateManageSkill.UpdateKeyValue();
            vm.FormSchema[0].Data[0].value = vm.UserSkillArray.row.TITLE;
            vm.FormSchema[0].Data[1].value = vm.UserSkillArray.row.SKILL_PROGRESS;
            vm.EmployeeProfiency = vm.UserSkillArray.row.SKILL_PROGRESS == null ? 0 : vm.UserSkillArray.row.SKILL_PROGRESS;
            vm.proficiencyManager = vm.UserSkillArray.row.SKILL_PROGRESS_BY_MANAGER == null ? 0 : vm.UserSkillArray.row.SKILL_PROGRESS_BY_MANAGER;
        }
        await vm.GetCommentList();
    },
    methods: {
        headerFormatTime: function (dateTimeString) {
            let dateTime = new Date(dateTimeString);

            // Options for formatting day, month, and year
            let options = {
                day: '2-digit',
                month: 'short',
                year: 'numeric'
            };

            // Format date to an array of parts
            let parts = dateTime.toLocaleDateString('en-GB', options).split(' ');

            // Construct the formatted date string with a comma
            let formattedDate = `${parts[0]} ${parts[1]}, ${parts[2]}`;

            return formattedDate;
        },
        toPrettyString(userDate) {
            const now = new Date();
            const date = new Date(userDate);
            const ts = Math.abs(now - date); // Time difference in milliseconds
            const delta = ts / 1000; // Convert milliseconds to seconds

            if (delta < 60) {
                return 'few seconds ago';
            }
            if (delta < 120) {
                return 'a minute ago';
            }
            if (delta < 2700) { // 45 * 60
                return `${Math.floor(delta / 60)} minutes ago`;
            }
            if (delta < 5400) { // 90 * 60
                return 'an hour ago';
            }
            if (delta < 86400) { // 24 * 60 * 60
                return `${Math.floor(delta / 3600)} hours ago`;
            }
            if (delta < 172800) { // 48 * 60 * 60
                return 'yesterday';
            }
            if (delta < 2592000) { // 30 * 24 * 60 * 60
                return `${Math.floor(delta / 86400)} days ago`;
            }
            if (delta < 31104000) { // 12 * 30 * 24 * 60 * 60
                const months = Math.floor(delta / (30 * 86400));
                return months <= 1 ? '1 month ago' : `${months} months ago`;
            }
            const years = Math.floor(delta / (365 * 86400));
            return years <= 1 ? '1 year ago' : `${years} years ago`;
        },
        formatTime(dateTimeString) {
            // Create a Date object from the provided date and time string
            let dateTime = new Date(dateTimeString);

            // Format the time using toLocaleTimeString()
            let formattedTime = dateTime.toLocaleTimeString([], {
                hour: '2-digit',
                minute: '2-digit'
            });

            return formattedTime;
        },
        onSlide() {
            vm.proficiencyManager = this.proficiencyManager;
        },
        ClosePopUp() {
            var vm = this;
            vm.$parent.isManageUserSkill = false;
        },
        SaveData() {
            var vm = this;
            var userSkillId = vm.UserSkillArray.row.USER_SKILL_ID;
            var params = `userSkillId=${userSkillId}&skillProgress=${vm.EmployeeProfiency}&proficiencyByManager=${vm.proficiencyManager}`
            vm.$refs.validateManageSkill.validateForm.validate().then(result => {
                if (result) {
                    DataService.UpdateUserSkill(params).then((response) => {
                        if (response.data != null) {
                            if (response.data.Msg == "UPDATE" && response.data.Code == "1") {
                                vm.ShowAlert(vm.$t('UserProgress'), "success");
                                vm.ClosePopUp();
                                vm.callbackfunction();
                                vm.isLoading = false;
                            } else {
                                vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                vm.isLoading = false;
                            }
                        }
                    });
                }
            });
        },
        SaveComment() {
            var vm = this;
            var userSkillId = vm.UserSkillArray.row.USER_SKILL_ID;
            var comment = vm.$refs.validateManageComment.getPropertyValue("Comment", "value");
            var params = `userSkillId=${userSkillId}&sourceName=SKILL&comment=${comment}`
            vm.$refs.validateManageComment.validateForm.validate().then(result => {
                if (result) {
                    DataService.SaveComment(params).then((response) => {
                        if (response.data != null) {
                            if (response.data.Msg == "INSERT" && response.data.Code == "1") {
                                vm.ShowAlert(vm.$t('UserProgress'), "success");
                                vm.GetCommentList();
                                vm.CommentSchema[0].Data[0].value = '';
                                vm.$refs.validateManageComment.validateForm.reset();
                                vm.isLoading = false;
                            } else {
                                vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                vm.isLoading = false;
                            }
                        }
                    });
                }
            });
        },
        async GetCommentList() {
            var vueObj = this;
            vueObj.isLoading = true;
            var userSkillId = vueObj.UserSkillArray.row.USER_SKILL_ID;
            var params = `userSkillId=${userSkillId}&sourceName=SKILL`
            await DataService.CommentList(params).then(response => {
                if (response.data.Status == "Success") {
                    if (response.data != "" && response.data != null) {
                        if (response.data.Result.data.length > 0) {
                            vueObj.CommentList = response.data.Result.data;
                            vueObj.TotalRecords = response.data.Result.data[0].TOTALRECORDS;
                        } else {
                            vueObj.TotalRecords = 0
                            vueObj.CommentList = [];
                        }
                    } else {
                        vueObj.TotalRecords = 0
                        vueObj.CommentList = [];
                    }
                }
            })
        }
    },
});
</script>

<style>
.vue-slider-rail {
    background-color: #5bc0de !important;
}

.vue-slider-dot-handle {
    background-color: #5bc0de !important;
}

.conversation-box .name-circle {
    width: 61px !important;
    height: 61px !important;
    line-height: 41px !important;
}

textarea {
    resize: none !important;
}

.conversation-box .name-circle img {
    left: 2px !important;
    top: 2px !important;
}

.vue-slider-dot-tooltip-inner {
    position: absolute !important;
    top: 100% !important;
}
</style>
