<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h5 name="header" class="pt-2">
                                {{ $t('Manage_User_Skill') }}
                            </h5>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="listing">

                                <!-- Listing -->
                                <div class="table-responsive table-fix-header" style="max-height:500px">
                                    <table class="table table-bordered dt-responsive nowrap mt-0">
                                        <thead class="thead-bg">
                                            <tr class="text-capitalize">
                                                <th style="width:5%" class="text-center">
                                                    <label class="form-check form-check-inline">
                                                        <div
                                                            class="custom-control custom-checkbox custom-control-inline">
                                                            <input type="checkbox"
                                                                style="z-index: 1; width: 19px; margin-top: 6px;"
                                                                class="chkitems custom-control-input"
                                                                :checked="selectAll" @change="selectAllItems"
                                                                name="selectedItems" value="asd">
                                                            <label
                                                                class="custom-control-label universal-custom-control-label pt-1"></label>
                                                        </div>
                                                    </label>
                                                </th>
                                                <th scope="col">Title</th>
                                                <th scope="col" class="text-center">Employee Proficiency</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="SkillList.length === 0">
                                                <td class="text-center text-danger no-record" colspan="8">
                                                    {{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>

                                            <!-- Skill list rows -->
                                            <template v-else>
                                                <tr v-for="(item, index) in SkillList" :key="index">
                                                <td class="text-center">
                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                    <input
                                                        type="checkbox"
                                                        :id="'tabletablebordered' + index"
                                                        @change="updateSelectAll"
                                                        @click="NewSeletedIds(item.SKILL_ID)"
                                                        class="chkitems custom-control-input"
                                                        v-model="selectedItems"
                                                        :value="item.SKILL_ID"
                                                        name="selectedItems"
                                                    />
                                                    <label
                                                        :for="'tabletablebordered' + index"
                                                        class="custom-control-label"
                                                    ></label>
                                                    </div>
                                                </td>
                                                <td>{{ item.TITLE }}</td>
                                                <td class="text-center">
                                                    {{ item.SKILL_PROGRESS }}<span>%</span>
                                                </td>
                                                </tr>
                                            </template>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="SaveData()"
                                title="Save"><i class="fa fa-floppy-o pr-2"></i>Save</a>
                            <a href="javascript:;" class="btn btn-danger delete-btn" v-on:click="ClosePopUp()"
                                title="Cancel"><i class="fa fa-times pr-2"></i>Cancel</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        UserId: {
            type: Number,
            required: false
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            isLoading: false,
            SkillList: [],
            selectedItems: [],
            selectAll: false,
            noRecord: false,
            TotalRecords: 0,
            Newids:[]
        }
    },

    async created() {
        var vm = this;
        await vm.GetSkillList();
    },

    methods: {
        selectAllItems() {
            var vueObj = this;
            this.selectAll = !this.selectAll;
            if (this.selectAll) {
                this.SkillList.forEach(function (item) {
                    vueObj.selectedItems.push(item.SKILL_ID);
                })
            } else {
                this.selectedItems = [];
            }
        },
        updateSelectAll() {
            var vm = this;
            const totalItems = this.SkillList.length;
            const selectedCount = this.selectedItems.length;
            vm.selectAll = totalItems > 0 && selectedCount === totalItems;
        },
        NewSeletedIds(id) {
            const vm = this;            
            vm.Newids.push(id);
            setTimeout(() => {
                const isSelected = vm.selectedItems.includes(id);

                if (!isSelected) {
                    vm.Newids = vm.Newids.filter(item => item !== id);
                }
            }, 400);            
        },
        async GetSkillList() {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `id=${vueObj.UserId}&reqFrom=USER_PROFILE`;
            await DataService.ListSkillForAssignment(params).then(response => {
                if (response.data.Status == "Success") {
                    if (response.data != "" && response.data != null) {
                        if (response.data.Result.data.length > 0) {
                            vueObj.SkillList = response.data.Result.data;
                            vueObj.TotalRecords = response.data.Result.data[0].TOTALRECORDS;
                            vueObj.noRecord = false;
                            vueObj.IsLoad = true;
                            response.data.Result.data.forEach(function (item) {
                                if (item.USER_SKILL_ID > 0) {
                                    vueObj.selectedItems.push(item.SKILL_ID);
                                }
                            })
                            const totalItems = this.SkillList.length;
                            const selectedCount = this.selectedItems.length;
                            vueObj.selectAll = totalItems > 0 && selectedCount === totalItems;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                            vueObj.SkillList = [];
                        }

                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.SkillList = [];
                    }
                }
            })
        },
        async SaveData() {
            var vm = this;
            if (this.selectedItems.length === 0) {
                vm.ShowAlert(vm.$t("PleaseSelectOneSkill"), "warning", vm.$t('Alert'));
                return;
            }
            const filteredSkills = vm.SkillList.filter(skill => this.selectedItems.includes(skill.SKILL_ID));
            filteredSkills.forEach(skill => {if (vm.Newids.includes(skill.SKILL_ID) && skill.STATUS_ID === 1002) {skill.STATUS_ID = 1001; } });

            filteredSkills.forEach(skill => {skill.STATUS_ID = 1001});
            
            var obj = JSON.stringify(filteredSkills);
            var requestPayload = { objData: obj };
            await DataService.AddUpdateUserSkillAssignment(requestPayload).then(response => {
                if (response.data != null) {
                    if (response.data.Status == "Success" && response.data.Code == "1") {
                        vm.ShowAlert(vm.$t('SkillSuccess'), "success");
                        vm.ClosePopUp();
                        vm.callbackfunction();
                        vm.isLoading = false;
                    }
                    else {
                        vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        vm.isLoading = false;
                    }
                }
            });
        },
        ClosePopUp() {
            var vm = this;
            vm.$parent.isAddUserSkill = false;

        }
    }
}
</script>