<template lang="en">
    <div>
        <section id="main-content" class="main-content">
            <section class="main-content">
                <div class="container-fluid p-0">
                   
                   <div class="border col-lg-12 mb-3">
                    
                        <div class="col-lg-12 p-0 mt-3">
                            <dynamic-form ref="validateUserBreak" lang="en" :buttons="false" :schema="FormSchema">
                            </dynamic-form>
                        </div>
                   
                    <div class="row flex-row-reverse my-3 ">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" class="btn btn-success save-btn mr-2" v-on:click="SaveData()"
                                                    title="Save"><i class="fa fa-save pr-2"></i>Save</a>
                                <a href="javascript:;" class="btn btn-danger mr-2" v-on:click="cancel()"
                                                    title="Cancel"><i class="fa fa-close pr-2"></i>Cancel</a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">                                   
                                        <span class="mandatory">{{ $t("Mandatory") }}</span>                                   
                                </div>
                            </div>
                    </div>
                </div>
                    
                        <div class="conversation-date heading-border">
                            <span>{{$t("UserBreaks")}}</span>
                        </div>
                        <tg-list ModuleName="User" SubModuleCode="Break"
                                                IdentityColumn="USERBREAKID" :listType="['List']" :ListData="UserBreakListing"
                                                :HeaderData="Headers" 
                                                :showCheckBox="true"
                                                :TotalRecords="TotalRecords"
                                                :IsShowAction="false" 
                                                :RenderRowActionMethod="() => { }"
                                                :ListDataCallBackFunction="GetUserBreakList"
                                                :NorecordfoundText="$t('NoRecordfound')"
                                                :HeaderButtons="HeaderButtons" 
                                                v-on:HeaderButtonClick="commonHeaderButtonClick"
                                                v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata">
                                                <template v-slot:slotdata="{ data }">
                                                    <template v-if="data.column.COLUMN_NAME == 'STARTTIME'">
                                                        <span>
                                                            {{ $options.filters.formatTimeFunc(data.row.STARTTIME) }}
                                                        </span>
                                                    </template>
                                                            <template v-if="data.column.COLUMN_NAME == 'ENDTIME'">
                                                        <span>
                                                            {{ $options.filters.formatTimeFunc(data.row.ENDTIME) }}
                                                        </span>
                                                    </template>
                                                            <template v-if="data.column.COLUMN_NAME == 'BREAKNAME'">
                                                        <span>
                                                            <a href="javascript:;" v-on:click="EditUserBreakById(data)">
                                                                {{ data.row.BREAKNAME }}</a>
                                                        </span>
                                                    </template>
                                                            <template v-if="data.column.COLUMN_NAME == 'ISBILLABLE'">
                                                        <span>
                                                            <a class="Bllable single-action" v-if="data.row.ISBILLABLE == false" v-on:click="UpdateBillable(data)" href="javascript:;"
                                                             title="Non-Billable" IsBillable="true">
                                                            <em class="fa fa-close text-danger action-icon">
                                                            </em>
                                                            </a>
                                                            <a class="Bllable single-action" v-if="data.row.ISBILLABLE == true" v-on:click="UpdateBillable(data)" href="javascript:;"
                                                                title="Billable" IsBillable="false">
                                                                <em class="fa fa-check text-success action-icon">
                                                                </em>
                                                            </a>
                                                        </span>
                                                    </template>
                                                </template>
                        </tg-list>
</div>
</section>
</section>
</div>
</template>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>

<script>
import { defineComponent } from '@vue/composition-api'
import DataService from '../../services/DataService';
export default defineComponent({
    props: {
        UserId: {
            type: Number
        },
        Items: {
            type: Array
        },
        emitName: {
            type: String
        },
        ModuleName: {
            type: String
        },
        SubModule: {
            type: String
        },
        title: {
            type: String
        },
        recordId: {
            type: Number
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            UserBreakListing: [],
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            UBid: 0,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('BreakName'),
                            name: "BreakName",
                            value: "",
                            placeholder: "Break Name",
                            validationRules: {
                                "required": true,
                                "regex":/^[a-zA-Z0-9\s]*$/
                            },
                            disabled: false,
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EventStartTime'),
                            name: "StartTime",
                            value: "",
                            placeholder: this.$t('EventStartTime'),
                            validationRules: { "required": true, max: 100 },
                            config: {
                                mode: "time",
                                format: "hh:mm A",
                                minDate: new Date()
                            }
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndTime'),
                            name: "EndTime",
                            value: "",
                            placeholder: this.$t('EndTime'),
                            validationRules: { "required": true, max: 100 },
                            config: {
                                mode: "time",
                                format: "hh:mm A",
                                minDate: new Date()
                            }
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('Isbillable'),
                            name: "IsBillable",
                            value: "",
                            placeholder: this.$t('Isbillable'),
                            validationRules: {
                                "required": false
                            },
                        }
                    ],
                },
            ],
            Headers: [
                {
                    "COLUMN_NAME": "ROWNUMBER",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "#",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "BREAKNAME",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Break Name",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "SHIFTNAME",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SHIFT NAME",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "STARTTIME",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "START TIME",
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": "ENDTIME",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "END TIME",
                    "DISPLAY_ORDER": 5
                },
                {
                    "COLUMN_NAME": "ISBILLABLE",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "IS BILLABLE",
                    "DISPLAY_ORDER": 6
                }],
            HeaderButtons:
                [
                    {
                        title: 'Delete',
                        iconClass: 'fa-trash-o',
                        callbackfunction: 'commonDeleteData',
                        additionalClass: '',
                        isdisabled: true,
                        isvisible: true,
                        checkPrivilege: false,
                        id: 'DeleteMultiple',
                        isEnabledOnSelectedRow: true
                    }
                ],
            isLoading: false,
            shiftStartTime: '',
            shiftEndTime: '',
            shiftId: 0,
            startTime: '',
            endTime: ''
        }
    },
    async created() {
        var vm = this;
        await vm.GetUserBreakList();
    },
    methods: {
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.DeleteUserBreak();
                    break;
            }
        },
        DeleteUserBreak: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Break")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "USER",
                        refCode: "TALYGEN_user_break"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.GetUserBreakList();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Break')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Break')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('Break')]), "failure", true, vm.$t('Alert'));
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetUserBreakList();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.GetUserBreakList();
        },

        async SaveData() {
            var vm = this;
            const result = await vm.$refs.validateUserBreak.validateForm.validate();
            if (result) {
                vm.startTime = vm.$refs.validateUserBreak.getPropertyValue("StartTime", "value");
                vm.endTime = vm.$refs.validateUserBreak.getPropertyValue("EndTime", "value");
                var sTime = vm.$options.filters.formatDateTime(vm.startTime);
                var eTime = vm.$options.filters.formatDateTime(vm.endTime);
                var isShiftValid = await vm.checkShiftTiming();
                if (isShiftValid) {
                    var objData =
                    {
                        UBid: vm.UBid,
                        Uid: vm.UserId,
                        breakName: vm.$refs.validateUserBreak.getPropertyValue("BreakName", "value"),
                        shiftId: vm.shiftId,
                        startTime: sTime,
                        endTime: eTime,
                        isBillable: vm.$refs.validateUserBreak.getPropertyValue("IsBillable", "value"),
                        companybreakId: 0,
                    }
                    var obj = JSON.stringify(objData);
                    var params = `objData=${obj}`;
                    await DataService.AddUpdateUserBreak(params).then(response => {
                        if (response.data != null) {
                            if (response.data.Status == "Success" && response.data.Code == "1") {
                                vm.ShowAlert(vm.$t('AddedUserBreak'), "success");
                                vm.clearFields();
                                vm.GetUserBreakList();
                                vm.isLoading = false;
                            } else if (response.data.Status == "Success" && response.data.Code == "2") {
                                vm.ShowAlert(vm.$t('UpdatedUserBreak'), "success");
                                vm.clearFields();
                                vm.GetUserBreakList();
                                vm.isLoading = false;
                            } else {
                                vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                vm.isLoading = false;
                            }
                        }
                    });

                }
            }

        },
        UpdateBillable(data) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmUpdateStatusRecord"), true, (vm.$t("ConfirmUpdateStatusRecord")), function () {
                let userBreakId = data.row.USERBREAKID;
                let isBillable = data.row.ISBILLABLE;
                vm.isLoading = true;
                var url = `userBreakId=${userBreakId}&isBillable=${isBillable}`;
                DataService.UpdateBillableStatus(url).then(function (response) {
                    if (response.data.Status == "Success") {
                        vm.ShowAlert(vm.$t('UpdatedUserBreak'), "success");
                        vm.GetUserBreakList();
                        vm.isLoading = false;
                    }
                });
            })
        },
        clearFields: function () {
            var vm = this;
            vm.FormSchema[0].Data[0].value = "";
            vm.FormSchema[0].Data[1].value = "";
            vm.FormSchema[0].Data[2].value = "";
            vm.FormSchema[0].Data[3].value = "";
            vm.$refs.validateUserBreak.validateForm.reset();
        },
        EditUserBreakById(data) {
            var vm = this;
            vm.UserId = data.row.USERID
            vm.shiftId = data.row.SHIFTID;
            vm.UBid = data.row.USERBREAKID;
            function parseTime(dateTimeString) {
                var dateTimeParts = dateTimeString.split('T');
                if (dateTimeParts.length !== 2) {
                    return null; // Invalid datetime format
                }
                var timeString = dateTimeParts[1]; // Extract time part
                var time = timeString.match(/(\d+):(\d+):(\d+)/);
                if (!time) {
                    return null;
                }
                var hours = parseInt(time[1], 10);
                var minutes = parseInt(time[2], 10);
                var seconds = parseInt(time[3], 10);
                // Determine AM/PM
                var ampm = hours >= 12 ? 'PM' : 'AM';
                // Adjust hours to 12-hour format
                hours = hours % 12;
                hours = hours ? hours : 12; // the hour '0' should be '12'
                return { hours: hours, minutes: minutes, ampm: ampm };
            }
            let StartTime = parseTime(data.row.STARTTIME);
            let EndTime = parseTime(data.row.ENDTIME);
            vm.FormSchema[0].Data[0].value = data.row.BREAKNAME;
            vm.FormSchema[0].Data[1].value = StartTime;
            vm.FormSchema[0].Data[2].value = EndTime;
            vm.FormSchema[0].Data[3].value = data.row.ISBILLABLE;
        },
        async getShiftTiming() {
            var vm = this;
            if (vm.UserId) {
                try {
                    var url = `id=${vm.UserId}`;
                    await DataService.GetShiftTiming(url).then(function (response) {
                        if (response.data.Status == "Success") {
                            if (response.data.Result.DATA != 0) {
                                vm.shiftStartTime = response.data.Result.DATA[0].shiftStartTime;
                                vm.shiftEndTime = response.data.Result.DATA[0].shiftEndTime;
                                vm.shiftId = response.data.Result.DATA[0].SHIFT_ID;
                            }
                        }
                    });
                } catch (error) {
                    console.error("Error fetching shift timing:", error);
                }
            }
        },
        async checkShiftTiming() {
            var vm = this;
            await this.getShiftTiming();
            if (this.shiftId > 0) {
                const date = new Date();
                let breakStartTime = new Date(vm.startTime);
                let breakEndTime = new Date(vm.endTime);

                // Adjust break end time if it goes past midnight
                if (breakStartTime > breakEndTime) {
                    breakEndTime.setDate(breakEndTime.getDate() + 1);
                }

                let shiftStartTime = new Date(vm.shiftStartTime);
                let shiftEndTime = new Date(vm.shiftEndTime);

                // Format shift start time to AM/PM
                let formattedshiftStartTime = vm.formatTime(shiftStartTime);
                let formattedshiftEndTime = vm.formatTime(shiftEndTime);

                // Adjust shift end time if it goes past midnight
                if (shiftStartTime > shiftEndTime) {
                    shiftEndTime.setDate(shiftEndTime.getDate() + 1);
                }

                if (breakEndTime.getHours() > shiftEndTime.getHours()) {
                    breakEndTime.setDate(shiftEndTime.getDate() - 1);
                }
                // Validate break times
                if (breakEndTime < breakStartTime) {
                    vm.ShowAlert("Break start time should be less than break end time.", "warning", vm.$t('Alert'));
                    return false;
                }
                if (shiftStartTime.getHours() > 12) {

                    if (breakStartTime.getHours() < 12) {
                        if (breakStartTime > shiftStartTime || breakEndTime > shiftEndTime || breakStartTime > chkEndDateTime) {
                            vm.ShowAlert(`Break start time and break end time should be between shift start time ${formattedshiftStartTime} and shift end time ${formattedshiftEndTime}`, "warning", vm.$t('Alert'));
                            return false;
                        }
                    }
                    else {
                        if (breakStartTime < shiftStartTime || breakEndTime > shiftEndTime) {
                            vm.ShowAlert(`Break start time and break end time should be between shift start time ${formattedshiftStartTime} and shift end time ${formattedshiftEndTime}`, "warning", vm.$t('Alert'));
                            return false;
                        }
                        if (breakEndTime < shiftStartTime || breakEndTime > shiftEndTime) {
                            vm.ShowAlert(`Break start time and break end time should be between shift start time ${formattedshiftStartTime} and shift end time ${formattedshiftEndTime}`, "warning", vm.$t('Alert'));
                            return false;
                        }
                    }
                }
                else {
                    if (breakStartTime < shiftStartTime || breakStartTime > shiftEndTime) {
                        vm.ShowAlert(`Break start time and break end time should be between shift start time ${formattedshiftStartTime} and shift end time ${formattedshiftEndTime}`, "warning", vm.$t('Alert'));
                        return false;
                    }
                    if (breakEndTime < shiftStartTime || breakEndTime > shiftEndTime) {
                        vm.ShowAlert(`Break start time and break end time should be between shift start time ${formattedshiftStartTime} and shift end time ${formattedshiftEndTime}`, "warning", vm.$t('Alert'));
                        return false;
                    }
                }
            }
            else {
                // shiftId is not greater than 0
                vm.ShowAlert(vm.$t('AssignShiftUser'), "warning", vm.$t('Alert'));
                vm.clearFields();
                return false;
            }
            return true;
        },
        formatTime(date) {
            let hours = date.getHours();
            const minutes = date.getMinutes();
            const ampm = hours >= 12 ? 'PM' : 'AM';

            hours = hours % 12; // Convert to 12-hour format
            hours = hours ? hours : 12; // The hour '0' should be '12'
            const strMinutes = minutes < 10 ? '0' + minutes : minutes; // Add leading zero

            return `${hours}:${strMinutes} ${ampm}`;
        },
        async GetUserBreakList() {
            var vm = this;
            vm.isLoading = true;
            vm.UserBreakListing = [];
            var url = `id=${vm.UserId}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&lang=EN`;
            await DataService.GetUserBreakListing(url).then(function (response) {
                if (response.data.Status == "Success") {
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == 'STARTTIME') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'ENDTIME') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'BREAKNAME') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                        if (column.COLUMN_NAME == 'ISBILLABLE') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                    if (response.data.Result.DATA.length > 0) {

                        vm.UserBreakListing = response.data.Result.DATA;
                        vm.TotalRecords = response.data.Result.DATA[0].TOTALRECORDS;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        vm.IsLoad = true;
                    } else {
                        vm.noRecord = true;
                        vm.TotalRecords = 0
                        vm.UserBreakListing = [];
                    }
                }
                else {
                    vm.noRecord = true;
                    vm.TotalRecords = 0
                    vm.UserBreakListing = [];
                }
                setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            });
        },
        cancel() {
            var vm = this;
            vm.clearFields();
        },
    },
});
</script>
<style>
.vc-date {
    display: none !important;
}
</style>