<template>
    <section>
        <div class="my-popups" v-if="GetConfiguration.Action == undefined">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('Configuration') }} - {{ GetConfiguration.user_name }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>
                        <loader :isVisible="isLoading"></loader>
                        <div class="listing new_accordians_des" id="accordionEx">
                            <div class="accordion md-accordion left-filter"
                                v-for="(parentItem, parentIndex) in GetUserModulesKeysData" :key="keyupdate">
                                <div class="px-2 py-1 bg-light">
                                    <div class="card">
                                        <div class="card-header heading-border" role="tab"
                                            :id="'MainColumn' + parentIndex"
                                            @click="GetConfigurationData(parentItem.ModuleId, parentIndex)">
                                            <h5 class="mb-0"><a data-toggle="collapse" data-parent="#accordionEx"
                                                    :data-target="'#ColumnCollapse' + parentIndex" href="javascript:;"
                                                    :aria-expanded="parentIndex === getindex ? true : false"
                                                    :aria-controls="'ColumnCollapse' + parentIndex"
                                                    :class="parentIndex === getindex ? 'w-100' : 'collapased w-100'">
                                                    <span class="">
                                                        {{ parentItem.ModuleName }}</span><em
                                                        class="fa fa-angle-down rotate-icon"
                                                        aria-hidden="true"></em></a>
                                            </h5>
                                        </div>
                                        <div :id="'ColumnCollapse' + parentIndex"
                                            :class="parentIndex === getindex && buttons[0].isVisible ? 'collapse multi-collapse show' : 'collapse multi-collapse'"
                                            role="tabpanel" :aria-labelledby="'MainColumn' + parentIndex"
                                            data-parent="#accordionEx">
                                            <div class="col-12 mt-3">
                                            <table id="tablelistingdata" class="table table-bordered mt-0"
                                                v-if="noRecord" style="width:100%" aria-label="Folder Listing">
                                                <thead class="thead-bg"></thead>
                                                <tbody>
                                                    <tr v-if="noRecord" style="cursor:initial;">
                                                        <td colspan="4">
                                                            <div
                                                                class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                                {{ $t("NoRecordfound") }}</div>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                            <dynamic-form :schema="formSchema" :buttons="buttons" v-else
                                                v-on:OnSubmit="FinalSaveConfigurations"></dynamic-form>
                                        </div></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="listing new_accordians_des" id="accordionEx" v-if="GetConfiguration.Action == 'Edit'">
            <loader :isVisible="isLoading"></loader>
            <div class="accordion md-accordion left-filter" v-for="(parentItem, parentIndex) in GetUserModulesKeysData"
                :key="keyupdate">
                <div class="px-2 py-1 bg-light">
                    <div class="card">
                        <div class="card-header heading-border" role="tab" :id="'MainColumn' + parentIndex"
                            @click="GetConfigurationData(parentItem.ModuleId, parentIndex)">
                            <h5 class="mb-0"><a data-toggle="collapse" data-parent="#accordionEx"
                                    :data-target="'#ColumnCollapse' + parentIndex" href="javascript:;"
                                    :aria-expanded="parentIndex === getindex ? true : false"
                                    :aria-controls="'ColumnCollapse' + parentIndex"
                                    :class="parentIndex === getindex ? 'w-100' : 'collapased w-100'">
                                    <span class="">
                                        {{ parentItem.ModuleName }}</span><em class="fa fa-angle-down rotate-icon"
                                        aria-hidden="true"></em></a>
                            </h5>
                        </div>
                        <div :id="'ColumnCollapse' + parentIndex"
                            :class="parentIndex === getindex && buttons[0].isVisible ? 'collapse multi-collapse show' : 'collapse multi-collapse'"
                            role="tabpanel" :aria-labelledby="'MainColumn' + parentIndex" data-parent="#accordionEx">
                            <div class="col-12 mt-3">
                            <table id="tablelistingdata" class="table table-bordered mt-0" v-if="noRecord"
                                style="width:100%" aria-label="Folder Listing">
                                <thead class="thead-bg"></thead>
                                <tbody>
                                    <tr v-if="noRecord" style="cursor:initial;">
                                        <td colspan="4">
                                            <div
                                                class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                {{ $t("NoRecordfound") }}</div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                            <dynamic-form :schema="formSchema" :buttons="buttons" v-else
                                v-on:OnSubmit="FinalSaveConfigurations"></dynamic-form>
                        </div></div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        GetConfiguration: {
            type: Array
        }
    },
    data() {
        return {
            isLoading:false,
            formSchema: [],
            getindex: 0,
            keyupdate: 0,
            noRecord: false,
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i> Save Settings",
                    isVisible: true
                },
            ],
            AttachmentFileDetail: [],
            CheckFileSave: false,
            CompanyLogo: null,
            GetUserModulesKeysData: [],
        }
    },
    async created() {
        var vm = this;
        await vm.GetUserModuleskeys();
        await vm.GetConfigurationData();
    },
    methods: {
        async GetUserModuleskeys() {
            var vm = this;
            vm.isLoading = true;
            var params = `userId=${vm.GetConfiguration.user_id}`;
            await DataService.GetUserModuleskeys(params).then(response => {
                if (response.data.Result.length > 0) {
                    vm.GetUserModulesKeysData = response.data.Result;
                }
            })
        },
        async GetConfigurationData(id, index) {
            var vm = this;
            var isTrue = false;
            vm.formSchema = [];
            vm.isLoading = true;
            vm.buttons[0].isVisible = false;
            if (vm.getindex === index) {
                vm.getindex = -1;
                isTrue = false
            } else if (index != undefined) {
                vm.getindex = index
                isTrue = true;
            } else {
                vm.getindex = 0
                id = vm.GetUserModulesKeysData[0].ModuleId;
                isTrue = true;
            }
            if (isTrue) {
                var params = `moduleId=${id}&projectuserId=${vm.GetConfiguration.user_id}&applicableFor=USR`;
                await DataService.GetConfigurationkeys(params).then(function (response) {
                    if (response != null && response.data != null && response.data.Result.length > 0) {
                        vm.noRecord = false 
                        vm.ConvertConfigurationDataIntoDynamicFormobj(response.data.Result);
                        vm.buttons[0].isVisible = true;
                    } else {
                        vm.noRecord = true;
                        vm.formSchema = [];
                    }
                    vm.isLoading = false;
                });
            }else{vm.isLoading = false;}
            vm.keyupdate += 1;
        },
ConvertConfigurationDataIntoDynamicFormobj: function (configurations) {
   
    var ths = this;
    ths.formSchema = [];
    configurations.sort((a, b) =>
        (ths.$t(a.CORE_CONFIG_KEY) > ths.$t(b.CORE_CONFIG_KEY)) ? 1 :
        ((ths.$t(b.CORE_CONFIG_KEY) > ths.$t(a.CORE_CONFIG_KEY)) ? -1 : 0)
    );

    if (configurations != null && configurations.length > 0) {
        let obj = {
            layoutType: 'double',
            groupId: 0,
            group_name: "",
            Data: []
        };

        configurations.forEach(function (item, index) {
            let validationRules = {};

            if (item.CORE_CONFIG_KEY !== "FSMRegionAccess") {
                validationRules["required"] = true;
            }

            let astype = item.ASTYPE;
            let options = [];
            var val = item.CORE_CONFIG_VALUE ?? "";

            if (item.CORE_CONFIG_OPTIONS != null) {
                options = ths.MakeArray(item.CORE_CONFIG_OPTIONS, 'radio', '^');
            }
            if (item.SELECT_OPTIONS != null && item.SELECT_OPTIONS != "") {
                options = ths.MakeNormalArray(item.SELECT_OPTIONS);
            }

            let comfigMode = "date";
            let placeholder = "Enter " + ths.$t(item.CORE_CONFIG_KEY);

            if (astype == "SelectField" || astype == "CustomSelectField" || astype == "TreeSelectField") {
                placeholder = "Select " + ths.$t(item.CORE_CONFIG_KEY);
            } else if (astype == "MultiSelectField") {
                val = [];
                let valArray = item.CORE_CONFIG_VALUE.split('^');
                valArray.forEach(function (x) {
                    val.push({
                        label: x,
                        value: x
                    });
                });
            } else if (astype == "CheckBoxField") {
                val = item.CORE_CONFIG_VALUE.split('^');
            }

            if (astype == "DateField") {
                comfigMode = "date";
            } else if (astype == "MultiSelectField") {
                comfigMode = 'tag';
            } else if (astype == "FileField") {
                var filePath = item.CORE_CONFIG_VALUE;
                var filename = filePath.split('/').pop();
            }

            let fieldObj = {
                customFieldId: item.CORE_CONFIG_ID,
                astype: astype,
                label: ths.$t(item.CORE_CONFIG_KEY),
                name: item.CORE_CONFIG_KEY,
                value: val,
                placeholder: placeholder,
                mode: comfigMode,
                config: {
                    maxSize: 5,
                    showSelectedFiles: [{
                        attachmentName: filePath,
                        attachmentPath: filename
                    }],
                    onChange: ths.onChangeField,
                    onDeleteSelectedFiles: ths.onDeleteSelectedFiles,
                    onClickSelectedFiles: ths.showCompanyLogo,
                    accept: [
                        { fileType: 'png', iconClass: 'fa-file-image-o' },
                        { fileType: 'jpg', iconClass: 'fa-file-image-o' },
                        { fileType: 'jpeg', iconClass: 'fa-file-image-o' },
                    ],
                    mode: comfigMode,
                    options: options,
                    dependentOn: null,
                    showWhen: null,
                    valueMapping: null,
                    fieldCode: null,
                    showCrossOnSelectedFiles: false,
                },
                visibility: true,
                validationRules: validationRules,
                dtCode: item.CORE_CONFIG_TYPE,
                userGuide: item.CORE_CONFIG_DESC
            };

            obj.Data.push(fieldObj);
        });

        ths.formSchema.push(obj);
    }
},


        async showCompanyLogo(item) {
            var vm = this;
            let a = document.createElement('a');
            a.target = '_blank';
            await vm.GetCompanyLogo(item.attachmentPath);
            a.href = vm.CompanyLogo;
            a.click();
        },
        async GetCompanyLogo(imageName) {
            var vm = this;
            var param = 'imagename=' + imageName;
            await DataService.GetCompanyLogo(param).then((response) => {
                vm.CompanyLogo = response.data;
            });
        },
        onChangeField: function (evt, x) {
            var vm = this;
            
            if(x.name == "AllowFaceRecoginitionInKioskMode"){
                if(!vm.GetConfiguration.face_recog_setup){
                    console.log("no face recog setup",evt,x);
                    
                vm.ShowAlert(vm.$t("firstFaceRecognition"), "failure", true, vm.$t("Alert"));
                x.value = "no";
                    //vm.GetConfiguration.face_recog_setup.AllowFaceRecoginitionInKioskMode = evt.target.checked;
                }
               else{
                
               }
            }
            if (x.astype == 'FileField') {
                // for (let index = 0; index < e.target.files.length; index++) {
                //     const element = e.target.files[index];                        
                //     vm.AttachmentFileDetail.push(element)
                // }
                try {
                    // vm.fileData = [];
                    var files = evt.target.files; // FileList object
                    for (var i = 0; i < files.length; i++) {
                        var f = files[i];
                        var reader = new FileReader();
                        // Closure to capture the file information.
                        reader.onload = (function (theFile) {
                            return function (e) {
                                var binaryData = e.target.result;
                                //Converting Binary Data to base 64
                                var base64StringEncoded = window.btoa(binaryData);

                                var fileName = f.name;
                                // vm.fileName = fileName;
                                var n = fileName.lastIndexOf(".");
                                var extensionName = fileName.substring(n + 1, fileName.length);

                                // vm.fileExt += "." + extensionName;
                                var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                                var fileSize = f.size;
                                // vm.fileSize = fileSize;
                                var fileObj = {
                                    attachement_mime: attachemnetMime,
                                    fileName: fileName,
                                    userId: vm.GetUserInfo.ID,
                                    companyId: vm.GetUserInfo.CompanyId,
                                    id: 0,
                                    moduleName: 'User', //vm.moduleCode
                                    subModuleCode: "",
                                    containerName: vm.GetUserInfo.ContainerName,
                                    base64String: base64StringEncoded,
                                    attachement_path: '',
                                    fieldName: x.name
                                };

                                vm.AttachmentFileDetail.push(fileObj);
                            };
                        })(f);
                        // Read in the image file as a data URL.
                        reader.readAsBinaryString(f);
                    }
                }
                catch (ex) {
                }
            }
        },
        onDeleteSelectedFiles: function (e, x) {
            var vm = this;
            vm.formSchema.forEach(function (group, index) {
                group.Data.forEach(function (item, index) {
                    if (item.astype == 'FileField') {
                        vm.$delete(item.config, 'showSelectedFiles');
                        item.value = '';
                    }
                })
            });
        },
        SendAttactment: async function () {
            var params = JSON.stringify(vm.AttachmentFileDetail);
            await DataService.UploadFile(params).then(function (response) {
                if (response.data && response.data.length > 0) {
                    vm.CheckFileSave = false;
                    vm.formSchema.forEach(function (group, index) {
                        group.Data.forEach(function (item, index) {
                            if (item.astype == 'FileField') {
                                if (item.name.toLowerCase() == 'companylogo') {
                                    var img = response.data[0].Name.split('/')[response.data[0].Name.split('/').length - 1]
                                    item.value = img;
                                }
                                else {
                                    item.value = response.data[0].Name;
                                }

                            }
                        })
                    });
                    vm.SaveConfigurations();
                }
            });
        },
        FinalSaveConfigurations: function () {
            var vm = this;
            vm.formSchema[0].Data.forEach(field => {
                if (field.dtCode && field.dtCode.toLowerCase() == 'image' && vm.AttachmentFileDetail.length > 0) {
                    vm.CheckFileSave = true;
                }
            })
            if (vm.CheckFileSave) {
                vm.SendAttactment();
            } else {
                vm.SaveConfigurations();
            }
        },
        SaveConfigurations: function () {
            var ths = this;
            var configObj = []
            ths.formSchema.forEach(function (group, index) {
                group.Data.forEach(function (item, index) {
                    var value = item.value;
                    if (item.astype == "CheckBoxField") {
                        value = (item.value.filter((x) => x != "")).join("^");
                    }
                    if (item.astype == 'MultiSelectField') {
                        let val = "";
                        if (typeof (item.value) == 'object') {
                            try {
                                if (typeof (item.value.length) != 'undefined' && item.value.length > 0) {
                                    item.value.forEach(function (item) {
                                        if (val == "") {
                                            val = item.value;
                                        } else {
                                            val = val + '^' + item.value;
                                        }
                                    })
                                } else {
                                    val = item.value.value
                                }
                            } catch (e) {
                                val = item.value.value
                            }
                        } else {
                            val = item.value
                        }
                        value = val;
                    }
                    var obj = {
                        DataId: item.customFieldId,
                        key: item.name,
                        DataValue: value
                    }
                    configObj.push(obj)
                });
            });
            var paramObj = {
                configData: configObj,
                conditionData: {
                    refCode: 'CompanySetting',
                    moduleName: 'User',
                    configUserId: ths.GetConfiguration.user_id
                }
            }
            var data = JSON.stringify(paramObj);
            DataService.UpdateUserConfigData(JSON.stringify(data)).then(function (response) {
                if (response != null && response.data != null && response.data == 1) {
                    ths.ShowAlert(ths.$t('RecordsSavedSuccessfully'), 'success');
                    if(ths.GetConfiguration.Action == undefined){
                    ths.ClosePopUp();}
                } else {
                    ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t("Alert"));
                }
            });
        },
        ClosePopUp() {
            var vm = this;
            vm.$parent.CloseConfiguration();
        },
    },
}
</script>
