<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            Feedback Details
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>
                    </div>
                    <div class="row p-2">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                            <div class="form-group">
                                <label>{{ resources.title }}:</label>
                                <div class="form-control bg-light">{{ oldFeedbackData.feedback_question }}</div>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                            <div class="form-group">
                                <label>{{ resources.createdBy }}:</label>
                                <div class="form-control bg-light">{{ oldFeedbackData.created_by }}</div>
                            </div>
                        </div>

                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                            <div class="form-group">
                                <label>{{ resources.date }}:</label>
                                <div class="form-control bg-light"> {{
                                    $options.filters.formatDate(oldFeedbackData.created_at) }}</div>
                            </div>
                        </div>

                        <div v-for="(feedback, index) in oldFeedbackData.Feedbackdetails" :key="index"
                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                            <div class="form-group">
                                <label v-if="!feedback.isShowName">{{ feedback.FEEDBACKFROMNAME }}:</label>
                                <label v-else>{{ resources.anonymousUser }}:</label>

                                <div class="form-control bg-light">{{ feedback.FEEDBACK_ANSWER }}</div>
                            </div>
                        </div>

                    </div>
                    <div class="row p-4">
                        <div class="col-md-12 d-flex justify-content-end">
                            <button v-on:click="ClosePopup()" type="button" class="btn btn-danger"><i
                                    class="fa fa-close pr-2"></i> {{ $t('Close') }}</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    props: {
        oldFeedbackData: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            resources: {
                title: 'Title',
                createdBy: 'Created By',
                date: 'Date',
                anonymousUser: 'Anonymous User'
            },

        };
    },
    created() {
        if (this.oldFeedbackData != null) {
            if (this.oldFeedbackData.is_anonymous == 0) {
                this.oldFeedbackData.is_anonymous = false;
            }
            if (this.oldFeedbackData.is_anonymous == 1) {
                this.oldFeedbackData.is_anonymous = true;
            }
            if (this.oldFeedbackData.status_id == 1001) {
                this.oldFeedbackData.status_id = 'Active'
            }
            if (this.oldFeedbackData.status_id == 1003) {
                this.oldFeedbackData.status_id = 'InActive'
            }

        }
    },
    methods: {

        ClosePopup() {
            var vm = this;
            vm.$parent.viewDetailfeedback = false;
        }
    }
};
</script>